import React, { useState, useMemo } from 'react';
import { motion, AnimatePresence } from 'framer-motion';

// Utility to shuffle array items
function shuffleArray(array) {
  return array
    .map(val => ({ val, sort: Math.random() }))
    .sort((a, b) => a.sort - b.sort)
    .map(({ val }) => val);
}

// Utility to split an array into chunks of a given size
function chunkArray(array, size) {
  const result = [];
  for (let i = 0; i < array.length; i += size) {
    result.push(array.slice(i, i + size));
  }
  return result;
}

/**
 * WordlistTest
 * @param {Object[]} wordlist - Array of word objects (with translations, definitions, etc.)
 * @param {string} userLang - Language code for translations (default: 'en')
 * @param {string} lessonID - Lesson identifier (e.g., "a0m1l1")
 * @param {string} moduleID - Module identifier
 * @param {Function} onTestComplete - Callback function upon test completion (receives overall score and incorrect answers)
 */
function WordlistTest({ wordlist, userLang = 'en', lessonID, moduleID, onTestComplete }) {
  // Global state to collect all answers (keyed by question id)
  const [answers, setAnswers] = useState({});

  // Pagination: max 16 questions per deck
  const pageSize = 16;
  const [currentPage, setCurrentPage] = useState(0);
  // Within a page (deck), we show one card at a time
  const [currentCardIndex, setCurrentCardIndex] = useState(0);
  // Show the overlay after finishing a deck (page)
  const [showOverlay, setShowOverlay] = useState(false);
  // Data for the overlay for the current deck (score and incorrect answers)
  const [pageOverlayData, setPageOverlayData] = useState(null);

  // Generate all test questions from the wordlist
  const allQuestions = useMemo(() => {
    const questions = [];
    const allTranslations = wordlist
      .flatMap(item =>
        Object.entries(item.translations || {}).map(([lang, value]) => ({
          lang,
          value,
        }))
      )
      .filter(({ lang }) => lang === userLang);
    const allDefinitions = wordlist
      .map(item => item.elementaryDefinition)
      .filter(Boolean);

    wordlist.forEach(item => {
      const correctTranslation = item.translations?.[userLang];
      const correctDefinition = item.elementaryDefinition;
      if (correctTranslation && correctDefinition) {
        // Randomly choose a translation or definition question
        const questionType = Math.random() < 0.5 ? 'translation' : 'definition';
        if (questionType === 'translation') {
          const wrongOptions = shuffleArray(
            allTranslations
              .filter(({ value }) => value !== correctTranslation)
              .map(({ value }) => value)
          ).slice(0, 3);
          const options = shuffleArray([correctTranslation, ...wrongOptions]);
          questions.push({
            id: `trans-${item.word}`,
            type: 'translation',
            task: 'Translate',
            question: item.word,
            hint: `Hint: ${correctDefinition}`,
            options,
            correctAnswer: correctTranslation,
          });
        } else {
          const wrongOptions = shuffleArray(
            allDefinitions.filter(def => def !== correctDefinition)
          ).slice(0, 2);
          const options = shuffleArray([correctDefinition, ...wrongOptions]);
          questions.push({
            id: `def-${item.word}`,
            type: 'definition',
            task: 'Find definition',
            question: item.word,
            hint: `Hint: ${
              (item.translations &&
                Object.entries(item.translations).find(
                  ([lang]) => lang === userLang
                )?.[1]) || 'No translation available'
            }`,
            options,
            correctAnswer: correctDefinition,
          });
        }
      }
    });
    return shuffleArray(questions);
  }, [wordlist, userLang]);

  // Group the questions into decks of size "pageSize"
  const questionPages = useMemo(() => chunkArray(allQuestions, pageSize), [allQuestions]);
  const currentDeck = questionPages[currentPage] || [];
  const currentQuestion = currentDeck[currentCardIndex] || null;

  // Save the answer for a question
  const handleAnswer = (qId, selectedValue) => {
    setAnswers(prev => ({ ...prev, [qId]: selectedValue }));
  };

  // Handle moving to the next card in the deck
  const handleNextCard = () => {
    // If this is the last card in the current deck,
    // then submit the deck and show an overlay with this deck's results.
    if (currentCardIndex === currentDeck.length - 1) {
      submitDeck();
    } else {
      setCurrentCardIndex(prev => prev + 1);
    }
  };

  // Compute deck (page) results, then show overlay
  const submitDeck = () => {
    const correctCount = currentDeck.reduce(
      (count, q) => (answers[q.id] === q.correctAnswer ? count + 1 : count),
      0
    );
    const deckScore = Math.round((correctCount / currentDeck.length) * 100);
    const incorrectAnswers = currentDeck
      .filter(q => answers[q.id] !== q.correctAnswer)
      .map(q => ({
        question: q.question,
        correctAnswer: q.correctAnswer,
        userAnswer: answers[q.id] || 'No Answer Selected',
      }));
    setPageOverlayData({ deckScore, incorrectAnswers });
    setShowOverlay(true);
  };

  // Called when the overlay "continue" button is clicked.
  // If more decks remain, move to the next deck.
  // If this was the last deck, compute overall results.
  const handleOverlayContinue = () => {
    // If more decks remain
    if (currentPage < questionPages.length - 1) {
      setCurrentPage(prev => prev + 1);
      setCurrentCardIndex(0);
      setShowOverlay(false);
    } else {
      // Final deck: compute overall results
      const totalCorrect = allQuestions.reduce(
        (count, q) => (answers[q.id] === q.correctAnswer ? count + 1 : count),
        0
      );
      const overallScore = Math.round((totalCorrect / allQuestions.length) * 100);
      const overallIncorrect = allQuestions
        .filter(q => answers[q.id] !== q.correctAnswer)
        .map(q => ({
          question: q.question,
          correctAnswer: q.correctAnswer,
          userAnswer: answers[q.id] || 'No Answer Selected',
        }));
      if (onTestComplete) {
        onTestComplete(overallScore, overallIncorrect);
      }
      // Optionally, update overlay data to show final results
      setPageOverlayData({ deckScore: overallScore, incorrectAnswers: overallIncorrect });
      // Keep the overlay visible as a final results screen.
    }
  };

  return (
    <div className="test-container">
      {/* Show the current card if no overlay */}
      {!showOverlay && currentQuestion && (
        <div className="flashcard-container">
          <motion.div
            className="card-wl"
            initial={{ opacity: 0, y: 20 }}
            animate={{ opacity: 1, y: 0 }}
            exit={{ opacity: 0 }}
          >
            <div className="card-content">
              <div className="card-header">
                <h4>{currentQuestion.task}</h4>
                <p className="card-word">{currentQuestion.question}</p>
                <p className="hint">{currentQuestion.hint}</p>
              </div>
              <div className="card-body">
                {currentQuestion.options.map((option, idx) => (
                  <label key={`${currentQuestion.id}-${idx}`} className="option-label">
                    <input
                      type="radio"
                      name={currentQuestion.id}
                      value={option}
                      checked={answers[currentQuestion.id] === option}
                      onChange={() => handleAnswer(currentQuestion.id, option)}
                      className="option-input"
                    />
                    {option}
                  </label>
                ))}
              </div>
            </div>
            <div className="card-footer">
              <p>
                Card {currentPage * pageSize + currentCardIndex + 1} of {allQuestions.length}
              </p>
              <button onClick={handleNextCard} className="next-button">
                {currentCardIndex === currentDeck.length - 1 ? 'Submit Deck' : 'Next Card'}
              </button>
            </div>
          </motion.div>
        </div>
      )}

      {/* Overlay to show deck results or final test results */}
      <AnimatePresence>
        {showOverlay && pageOverlayData && (
          <motion.div
            className="overlay"
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            exit={{ opacity: 0 }}
            style={{
              position: 'fixed',
              top: 0,
              left: 0,
              width: '100vw',
              height: '100vh',
              background: 'rgba(0, 0, 0, 0.6)',
              display: 'flex',
              justifyContent: 'center',
              alignItems: 'center',
              zIndex: 1000,
            }}
          >
            <div
              className="overlay-content"
              style={{
                background: '#fff',
                borderRadius: '8px',
                padding: '2rem',
                maxWidth: '90%',
                maxHeight: '90%',
                overflowY: 'auto',
                textAlign: 'center',
              }}
            >
              <h2>
                {currentPage < questionPages.length - 1
                  ? `Deck ${currentPage + 1} Results`
                  : 'Final Test Results'}
              </h2>
              <p className="score">Score: {pageOverlayData.deckScore}%</p>
              {pageOverlayData.incorrectAnswers.length > 0 && (
                <div className="review-section">
                  <h3>Incorrect Answers:</h3>
                  <ul className="review-list">
                    {pageOverlayData.incorrectAnswers.map((item, idx) => (
                      <li key={idx} className="review-item">
                        <p>
                          <strong>Question:</strong> {item.question}
                        </p>
                        <p>
                          <strong>Your Answer:</strong> {item.userAnswer}
                        </p>
                        <p>
                          <strong>Correct Answer:</strong> {item.correctAnswer}
                        </p>
                      </li>
                    ))}
                  </ul>
                </div>
              )}
              <button onClick={handleOverlayContinue} className="next-button">
                {currentPage < questionPages.length - 1 ? 'Next Deck' : 'Finish Test'}
              </button>
            </div>
          </motion.div>
        )}
      </AnimatePresence>
    </div>
  );
}

export default WordlistTest;
