import React, { useEffect, useState } from 'react';
import FullCalendar from '@fullcalendar/react';
import timeGridPlugin from '@fullcalendar/timegrid';
import dayGridPlugin from '@fullcalendar/daygrid';

const CalendarComponent = () => {
  const [events, setEvents] = useState([]);

  const API_KEY = 'AIzaSyAqIYaOR3_hPVLsdE1P9kW5gzL28x3kbgE';
  const CALENDAR_1 = 'vladyslavced@gmail.com';
  const CALENDAR_2 = '2c66837d62268da6ce5968dc0fcb60fde397cefc27f5b226d04e60deb4f85634@group.calendar.google.com';

  //      Google Calendar
  const fetchEvents = async (calendarId, startTime, endTime) => {
    const url = `https://www.googleapis.com/calendar/v3/calendars/${calendarId}/events`;
    const params = new URLSearchParams({
      key: API_KEY,
      timeMin: startTime,
      timeMax: endTime,
    });

    try {
      const response = await fetch(`${url}?${params.toString()}`);
      const data = await response.json();

      //   items  
      if (!data || !data.items) {
        console.error('No events found:', data);
        return [];
      }

      //  
      return data.items.map((event) => ({
        id: event.id,
        title: event.summary || 'Occupied',
        start: event.start.dateTime || event.start.date,
        end: event.end.dateTime || event.end.date,
      }));
    } catch (error) {
      console.error('Error fetching events:', error);
      return [];
    }
  };

  //     
  const loadEvents = async () => {
    const now = new Date();
  
    //     
    const startOfWeek = new Date(now.setDate(now.getDate() - now.getDay() + 1)); // 
    const endOfWeek = new Date(now.setDate(now.getDate() - now.getDay() + 31)); // 
  
    //   
    startOfWeek.setHours(0, 0, 0, 0);
    endOfWeek.setHours(23, 59, 59, 999);
  
    const startTime = startOfWeek.toISOString();
    const endTime = endOfWeek.toISOString();
  
    //     
    const events1 = await fetchEvents(CALENDAR_1, startTime, endTime);
    const events2 = await fetchEvents(CALENDAR_2, startTime, endTime);
  
    //    
    const combinedEvents = [...events1, ...events2].sort(
      (a, b) => new Date(a.start) - new Date(b.start)
    );
    setEvents(combinedEvents);
  };
  

  useEffect(() => {
    loadEvents();
  }, []);

  return (
    <div>
      <h1>Teacher's timetable</h1>
      <FullCalendar
        plugins={[timeGridPlugin, dayGridPlugin]}
        initialView="timeGridWeek"
        slotMinTime="10:00:00"
        slotMaxTime="20:00:00"
        events={events}
        allDaySlot={false}
        height="auto"
        timeZone="auto"
      />
    </div>
  );
};

export default CalendarComponent;
