
import React, { useState, useEffect } from 'react';
import { useAuth, db } from '../contexts/AuthContext';
import { collection, doc, setDoc, deleteDoc } from 'firebase/firestore';

import Register from '../authpanel/Register';
import SignIn from '../authpanel/SignIn';
import { runWfpWdgt  } from '../components/wfp';
import Overlay from '../components/Overlay';  // Adjust the path based on where it's defined

import '../styles/PricingPage.css';  // Custom styles for pricing
import diamond from '../assets/prices/diamond-yellow.svg';
import crown from '../assets/prices/crown.svg';

import touch from '../assets/prices/touch.svg';
import limit from '../assets/prices/part.svg';
import solo from '../assets/prices/solo.svg';
import logic from '../assets/prices/logic.svg';

import topic from '../assets/prices/topic.svg';
import grammar from '../assets/prices/grammar.svg';
import speaking from '../assets/prices/speaking.svg';
import reading from '../assets/prices/reading.svg';
import vocabulary from '../assets/prices/vocabulary.svg';
import writing from '../assets/prices/writing.svg';
import listening from '../assets/prices/listening.svg';

import caution from '../assets/prices/caution.svg';

// Define the tiers with prices
const tiers = [
  { id: 'A0.Beginner', 
    stayerSub: "4 modules", moduleSub: "6 lessons + 6 workbooks", lessonPrice: 3, modulePrice: 8, bundlePrice: 15 },
  { id: 'A1.Elementary', 
    stayerSub: "4 modules", moduleSub: "6 lessons + 6 workbooks", lessonPrice: 4, modulePrice: 9, bundlePrice: 17 },
  { id: 'A2.Pre-intermediate', 
    stayerSub: "4 modules", moduleSub: "6 lessons + 6 workbooks", lessonPrice: 5, modulePrice: 10, bundlePrice: 19 },
  { id: 'B1.Intermediate', 
    stayerSub: "4 modules", moduleSub: "6 lessons + 6 workbooks", lessonPrice: 6, modulePrice: 11, bundlePrice: 21 },
  { id: 'B1+.Advanced intermediate', 
    stayerSub: "4 modules", moduleSub: "5 lessons + 5 workbooks", lessonPrice: 7, modulePrice: 12, bundlePrice: 23 },
  { id: 'B2.Upper-intermediate', 
    stayerSub: "3 modules", moduleSub: "7 lessons + 7 workbooks", lessonPrice: 8, modulePrice: 13, bundlePrice: 25 },
  { id: 'C1.Advanced', 
    stayerSub: "3 modules", moduleSub: "7 lessons + 7 workbooks", lessonPrice: 9, modulePrice: 14, bundlePrice: 27 },
]; 


const PricingPage = () => {
  const { user } = useAuth();
  const [isRegisterVisible, setRegisterVisible] = React.useState(false); 
  const [isLoginVisible, setLoginVisible] = React.useState(false);

  const [currentTier, setCurrentTier] = useState('');
  const [userTier, setUserTier] = useState(null);

  useEffect(() => {
    if (user && user.courseID) {
      const tier = tiers.find(t => t.id.startsWith(user.courseID));
      if (tier) {
        setCurrentTier(tier);
        setUserTier(tier); // Set the user's subscribed tier
      }
    }
  }, [user]);

  useEffect(() => {
    if (!document.getElementById('widget-wfp-script')) {
      const script = document.createElement('script');
      script.src = 'https://secure.wayforpay.com/server/pay-widget.js';
      script.id = 'widget-wfp-script';
      script.async = true;
      document.body.appendChild(script);
    }
    // No need to remove the script on unmount
  }, []);
  
  

  const handlePayment = async (tier, type) => {
    if (!user) {
      setRegisterVisible(true);
      return;
    }
  
    // Determine the correct amount and product name based on the type
    const amount = type === 'lesson' ? tier.lessonPrice :
                   type === 'module' ? tier.modulePrice :
                   tier.bundlePrice;
  
    const productName = `${tier.id}.${type === 'lesson' ? 'Single' :
                          type === 'module' ? 'Module' :
                          'Bundle'}`;
    const orderDate = Math.floor(Date.now() / 1000);
    const orderReference = `ORDER-${user.uid}-${productName}|${orderDate}`;  // Generate unique order reference
  
    // Prepare the payment details to pass to runWfpWdgt
    const paymentFields = {
      clientAccountId: user.uid,
      email: user.email,
      clientFirstName: user.displayName || 'John',
      clientLastName: user.lastName || 'Anon',
      phone: '380631234567',
      amount,
      orderReference,
      currency: 'USD',
      productName,
      orderDate
    };
  
    // Run the payment widget
    try {
      await runWfpWdgt(paymentFields);
  
      // Payment success logic goes here
      const paymentSuccess = true; // Replace with actual payment success check
      if (paymentSuccess) {
        console.log('Payment approved.');
      }
  
    } catch (error) {
      console.error('Payment initiation failed:', error);
      alert('Failed to initiate payment. Please try again.');
    }

    
  };
  
  
  const handleTierChange = (tier) => {
    setCurrentTier(tier);
  };

  const PricingPlan = ({ tier }) => (
    <>
      <div className="pricing-category">
        <h2 className="category-header">Single</h2>
        <div className="pricing-plan starter">
          <h3 className="pricing-header">1 Lesson + 1 Workbook</h3>
          <ul className="pricing-description">
            
            <li><img src={touch} alt='drop1' /><span>Interactive tasks </span></li>
            <li><img src={limit} alt='drop2' /><span>Limited insights into module's topic </span></li>
            <li><img src={logic} alt='drop3' /><span>Logically-structured tasks </span></li>
            <li><img src={solo} alt='drop4' /><span>You learn how to learn without a teacher </span></li>
            </ul>
          
  
          <span className="pricing-price">${tier.lessonPrice}</span>
          <button onClick={() => handlePayment(tier, 'lesson') } className="pricing-button">Buy Lesson</button>
        </div>
      </div>
  
      <div className="pricing-category">
        <div className="category-header"><img src={diamond} alt='mod'/> Module</div>
        <div className="pricing-plan greener">
          <h3 className="pricing-header">{tier.moduleSub}</h3>
          <ul className="pricing-description">
         
            <li><img src={topic} alt='drop1' /><span>~5 connected topics</span></li>
            <li><img src={grammar} alt='drop2' /><span>~3 grammar bundles</span></li>
            <li><img src={vocabulary} alt='drop3' /><span>Ultimate vocabulary boost</span></li>
            <li><img src={writing} alt='drop4' /><span>Secrets of writing like a Lord</span></li>
            <li><img src={speaking} alt='drop5' /><span>Solid speaking section</span></li>
            <li><img src={reading} alt='drop6' /><span>Reading section + techniques</span></li>
            <li><img src={listening} alt='drop7' /><span>Listening [in development...]</span></li>
            </ul>
        
          <span className="pricing-price">${tier.modulePrice}</span>
          <button onClick={(e) => { e.preventDefault(); handlePayment(tier, 'module')}} className="pricing-button">Buy Module</button>
        </div>
      </div>
  
      <div className="pricing-category">
        <div className="category-header"><img src={crown} alt='crown'/> Stayer</div>
        <div className="pricing-plan purpler">
          <h3 className="pricing-header">{tier.stayerSub}</h3>
          <div className="pricing-description notify">
            <div className='caution'><img src={caution} height="28px" alt='caution' /> <span>Caution!</span></div> This bundle contains half of the course. Extended learning for persistent learners.
          </div>
          <span className="pricing-price">${tier.bundlePrice}</span>
          <button onClick={() => handlePayment(tier, 'stayer')} className="pricing-button">Buy Bundle</button>
        </div>
      </div>
    </>
  );
  

  return (
    <div className="background">
  
      {isRegisterVisible && (
        <Overlay title="REGISTER" onClose={() => setRegisterVisible(false)}>
          <Register onClose={() => setRegisterVisible(false)} />
          <p>Already have an account? <span className='changeOver' onClick={() => { setRegisterVisible(false); setLoginVisible(true); }}>SIGN IN</span></p>
        </Overlay>
      )}
      {isLoginVisible && (
        <Overlay title="LOG IN" onClose={() => setLoginVisible(false)}>
          <SignIn onClose={() => setLoginVisible(false)} />
          <p>Don't have an account? <span className='changeOver' onClick={() => { setLoginVisible(false); setRegisterVisible(true); }}>REGISTER</span></p>
        </Overlay>
      )}
  
      <div className="container-price">
        {/* User's Current Subscription (if signed in and courseID exists) */}
        {user && userTier && (
          <div className="user-subscription">
            <div className='info'> Your Subscription Tier:</div>
            <h2 className="subscription-header"> {user.courseID}</h2>
            <div className="panel pricing-table">
              <PricingPlan tier={userTier} />
            </div>
            <h2 className="subscription-header gap">Compare to other courses:</h2>
          </div>
        )}
  
        {/* Tier Navigation */}
        <div className="radio-inputs">
          {tiers.map(tier => {
            const [level, description] = tier.id.split('.');
            return (
              <div className="radio" key={tier.id}>
                <input
                  type="radio"
                  id={`tier-${tier.id}`}
                  name="tier"
                  checked={tier.id === currentTier?.id}
                  onChange={() => handleTierChange(tier)}
                />
                <label
                  htmlFor={`tier-${tier.id}`}
                  className={`name ${tier.id === currentTier?.id ? 'active' : ''}`}
                >
                  <div className="detItem">{description.trim()}</div>
                  {level}
                </label>
              </div>
            );
          })}
        </div>
  
        {/* Pricing Panel */}
        {currentTier && (
          <div className="panel pricing-table">
            <PricingPlan tier={currentTier} />
          </div>
        )}
      </div>
    </div>
  );
}  
export default PricingPage;