// RenderWorkbookParts.js

import React from 'react';
import DOMPurify from 'dompurify';
import parse, { domToReact } from 'html-react-parser';
import RadioGroup from './RadioGroup';
import BatteryChargingComponent from './BatteryChargingComponent';

const RenderWorkbookParts = ({
  currentPartIndex,
  workbook,
  userInteractions,
  updateUserInteraction,
}) => {
  // 1) Build the prefix for the current part
  const currentPartPrefix = `Part${currentPartIndex + 1}`;

  // 2) The specific keys to render in this part
  const partOrder = [
    `${currentPartPrefix}/A.Warm-up`,
    `${currentPartPrefix}/A.task`,
    `${currentPartPrefix}/A.kit`,
    `${currentPartPrefix}/A.answer`,
    `${currentPartPrefix}/A.pic`,
    `${currentPartPrefix}/A.article`,
    `${currentPartPrefix}/A.articleAnswers`,
    `${currentPartPrefix}/A.audio`,
    `${currentPartPrefix}/A.hint`,
    `${currentPartPrefix}/A.transcription`,
    `${currentPartPrefix}/B.task`,
    `${currentPartPrefix}/B.kit`,
    `${currentPartPrefix}/B.answer`,
    `${currentPartPrefix}/C.task`,
    `${currentPartPrefix}/C.kit`,
    `${currentPartPrefix}/C.answers`,
    `${currentPartPrefix}/D.task`,
    `${currentPartPrefix}/D.kit`,
    `${currentPartPrefix}/D.answer`,
  ];

  // Filter only the keys that exist in the workbook data
  const currentPartKeys = partOrder.filter((key) => workbook[key]);
  if (currentPartKeys.length === 0) return null;

  // We'll increment this for each input we render, so each has a unique interactionKey.
  let inputIndex = 0;

  /**
   * Recursively find `data-correct` in or above the current node.
   * This allows a radio <input> that doesn't directly have `data-correct`
   * but is inside a parent <span data-correct="...">.
   */
  const findDataCorrectAttribute = (domNode) => {
    const visitedNodes = new Set();
    const dfs = (node) => {
      if (!node || visitedNodes.has(node)) return null;
      visitedNodes.add(node);
      if (node.attribs && node.attribs['data-correct']) {
        return node.attribs['data-correct'];
      }
      if (node.children && node.children.length > 0) {
        for (const child of node.children) {
          const found = dfs(child);
          if (found) return found;
        }
      }
      let sibling = node.next;
      while (sibling) {
        const foundSib = dfs(sibling);
        if (foundSib) return foundSib;
        sibling = sibling.next;
      }
      return dfs(node.parent);
    };
    return dfs(domNode);
  };

  /**
   * The parserOptions for html-react-parser.
   * Handles:
   *   - Radio Group in a <span data-correct="...">
   *   - Single radio, text input, textarea, select, and button elements.
   */
  const parserOptions = {
    replace: (domNode) => {
      if (domNode.type === 'tag') {
        const correctAnswer = findDataCorrectAttribute(domNode);

        // A) Radio Group in a <span data-correct="...">
        if (
          domNode.name === 'span' &&
          correctAnswer &&
          domNode.children.some(
            (child) =>
              child.type === 'tag' &&
              child.name === 'input' &&
              child.attribs.type === 'radio'
          )
        ) {
          const interactionKey = `${workbook.id}/${currentPartPrefix}/radioGroup${inputIndex++}`;
          const interaction = userInteractions?.[interactionKey] || { value: '', correct: false };
          return (
            <RadioGroup
              key={interactionKey}
              domNode={domNode}
              correctAnswer={correctAnswer}
              interactionKey={interactionKey}
              interaction={interaction}
              updateUserInteraction={updateUserInteraction}
              options={parserOptions}
            />
          );
        }

        // B) Single radio input
        if (
          domNode.name === 'input' &&
          domNode.attribs.type === 'radio' &&
          correctAnswer
        ) {
          const name = domNode.attribs.name || `radio${inputIndex}`;
          const interactionKey = `${workbook.id}/${currentPartPrefix}/${name}`;
          const interaction = userInteractions?.[interactionKey] || { value: '', correct: false };
          return (
            <input
              key={`${interactionKey}-${domNode.attribs.value}`}
              type="radio"
              name={name}
              value={domNode.attribs.value}
              checked={interaction.value === domNode.attribs.value}
              onChange={(e) => {
                const userVal = e.target.value;
                const isCorrect =
                  userVal.trim().toLowerCase() === correctAnswer.trim().toLowerCase();
                updateUserInteraction(interactionKey, { value: userVal, correct: isCorrect });
              }}
              className={interaction.correct ? 'correct' : interaction.value ? 'incorrect' : ''}
            />
          );
        }

        // C) Text input
        if (domNode.name === 'input' && domNode.attribs.type === 'text') {
          const possibleAnswers =
            typeof correctAnswer === 'string'
              ? correctAnswer.split(';').map((a) => a.trim().toLowerCase())
              : [];
          const interactionKey = `${workbook.id}/${currentPartPrefix}/${domNode.parent?.name}/input${inputIndex++}`;
          const interaction = userInteractions?.[interactionKey] || { value: '', correct: false };
          const inputWidth = correctAnswer ? correctAnswer.length + 5 : 10;
          return (
            <input
              key={interactionKey}
              type="text"
              data-correct={correctAnswer}
              value={interaction.value || ''}
              onChange={(e) => {
                const userVal = e.target.value.trim().toLowerCase();
                updateUserInteraction(interactionKey, {
                  value: e.target.value,
                  correct: possibleAnswers.includes(userVal),
                });
              }}
              className={interaction.correct ? 'correct' : interaction.value ? 'incorrect' : ''}
              style={{ width: `${inputWidth}ch` }}
              autoComplete="off"
              autoCorrect="off"
              autoCapitalize="none"
              spellCheck="false"
            />
          );
        }

        // D) Textarea
        if (domNode.name === 'textarea') {
          const interactionKey = `${workbook.id}/${currentPartPrefix}/${domNode.parent?.name}/textarea${inputIndex++}`;
          const interaction = userInteractions?.[interactionKey] || { value: '', correct: false };
          return (
            <textarea
              key={interactionKey}
              value={interaction.value || ''}
              onInput={(e) => {
                e.target.style.height = 'auto';
                e.target.style.height = `${e.target.scrollHeight}px`;
                e.target.style.overflowY = 'hidden';
              }}
              onChange={(e) => {
                const userVal = e.target.value.trim().toLowerCase();
                const isCorrect =
                  correctAnswer &&
                  userVal === correctAnswer.trim().toLowerCase();
                updateUserInteraction(interactionKey, {
                  value: e.target.value,
                  correct: !!isCorrect,
                });
              }}
              className={`textarea ${
                interaction.correct ? 'correct' : interaction.value ? 'yellow' : ''
              }`}
              placeholder={domNode.attribs?.placeholder || 'Type your answer...'}
              autoComplete="off"
              autoCorrect="off"
              spellCheck="false"
              style={{ resize: 'none' }}
            >
              {domToReact(domNode.children, parserOptions)}
            </textarea>
          );
        }

        // E) Select element
        if (domNode.name === 'select' && correctAnswer) {
          const interactionKey = `${workbook.id}/${currentPartPrefix}/${domNode.parent?.name}/select${inputIndex++}`;
          const interaction = userInteractions?.[interactionKey] || { value: '', correct: false };
          return (
            <select
              key={interactionKey}
              value={interaction.value || ''}
              onChange={(e) => {
                const val = e.target.value.trim().toLowerCase();
                const isCorrect = val === correctAnswer.trim().toLowerCase();
                updateUserInteraction(interactionKey, { value: e.target.value, correct: isCorrect });
              }}
              className={interaction.correct ? 'correct' : interaction.value ? 'incorrect' : ''}
            >
              {domToReact(domNode.children, parserOptions)}
            </select>
          );
        }

        // F) Button element
        if (domNode.name === 'button' && correctAnswer) {
          const interactionKey = `${workbook.id}/${currentPartPrefix}/${domNode.parent?.name}/button${inputIndex++}`;
          const interaction = userInteractions?.[interactionKey] || { value: '', correct: false };
          return (
            <button
              key={interactionKey}
              onClick={() => {
                const textVal =
                  domNode.attribs.value ||
                  domToReact(domNode.children, parserOptions).props.children ||
                  '';
                const isCorrect =
                  textVal.trim().toLowerCase() === correctAnswer.trim().toLowerCase();
                updateUserInteraction(interactionKey, { value: textVal, correct: isCorrect });
              }}
              className={interaction.correct ? 'correct' : interaction.value ? 'incorrect' : ''}
            >
              {domToReact(domNode.children, parserOptions)}
            </button>
          );
        }

        // Default: if there are children, let html-react-parser process them.
        if (domNode.children && domNode.children.length > 0) {
          return domNode;
        }
      }
    },
  };

  // --- Determine if this workbook part should use the reading grid layout ---
  // We check if any key (case-insensitive) includes "reading", "a.article", or "a.pic"
  const isReadingPart = currentPartKeys.some(
    (key) =>
      key.toLowerCase().includes('reading') ||
      key.toLowerCase().includes('a.article') ||
      key.toLowerCase().includes('a.pic')
  );

  if (isReadingPart) {
    let leftPic = null;
    let leftArticle = null;
    const rightElements = [];

    currentPartKeys.forEach((key, index) => {
      const htmlContent = workbook[key];
      if (!htmlContent) return;
      const sanitizedContent = DOMPurify.sanitize(htmlContent);
      const parsedContent = parse(sanitizedContent, parserOptions);

      // For the left frame: look for A.pic and A.article
      if (key.toLowerCase().includes('a.pic')) {
        // If the content looks like a URL, render it as an image.
        if (htmlContent.trim().startsWith('http')) {
          leftPic = (
            <div className="pic" key={index}>
              <img src={htmlContent.trim()} alt="Workbook visual" />
            </div>
          );
        } else {
          leftPic = (
            <div className="pic" key={index}>
              {parsedContent}
            </div>
          );
        }
      } else if (key.toLowerCase().includes('a.article')) {
        leftArticle = (
          <div className="article" key={index}>
            {parsedContent}
          </div>
        );
      } else {
        // Other content goes to the right frame.
        let contentType = 'part';
        if (key.includes('.Warm-up')) {
          contentType = 'warming';
        } else if (key.includes('.task')) {
          contentType = 'task';
        } else if (key.includes('.hint')) {
          contentType = 'hint';
        }

        if (contentType === 'warming') {
          rightElements.push(
            <BatteryChargingComponent key={index} htmlContent={htmlContent} />
          );
        } else if (contentType === 'task') {
          rightElements.push(
            <div className="task" key={index}>
              <h3>{parsedContent}</h3>
            </div>
          );
        } else if (contentType === 'hint') {
          rightElements.push(
            <div className="hint" key={index}>
              {parsedContent}
            </div>
          );
        } else {
          rightElements.push(
            <div className="part" key={index}>
              {parsedContent}
            </div>
          );
        }
      }
    });

    // Combine A.pic and A.article into one left content container.
    const leftContent = (
      <div className="left-content">
        {leftPic}
        {leftArticle}
      </div>
    );

    return (
      <div className="mane reading-grid">
        <div className="left-frame">
          {leftContent}
        </div>
        <div className="right-frame">
          {rightElements}
        </div>
      </div>
    );
  }

  // --- Default rendering for non-reading parts ---
  return (
    <div className="mane">
      {currentPartKeys.map((key, index) => {
        const htmlContent = workbook[key];
        if (!htmlContent) return null;
        let contentType = 'part';
        if (key.includes('.Warm-up')) {
          contentType = 'warming';
        } else if (key.includes('.article')) {
          contentType = 'article';
        } else if (key.includes('.task')) {
          contentType = 'task';
        } else if (key.includes('.hint')) {
          contentType = 'hint';
        }
        const sanitizedContent = DOMPurify.sanitize(htmlContent);
        const parsedContent = parse(sanitizedContent, parserOptions);
        if (contentType === 'warming') {
          return <BatteryChargingComponent key={index} htmlContent={htmlContent} />;
        } else if (contentType === 'article') {
          return (
            <div className="article" key={index}>
              {parsedContent}
            </div>
          );
        } else if (contentType === 'task') {
          return (
            <div className="task" key={index}>
              <h3>{parsedContent}</h3>
            </div>
          );
        } else if (contentType === 'hint') {
          return (
            <div className="hint" key={index}>
              {parsedContent}
            </div>
          );
        } else {
          return (
            <div className="part" key={index}>
              {parsedContent}
            </div>
          );
        }
      })}
    </div>
  );
};

export default RenderWorkbookParts;
