import React from 'react';
import './reading.css'; // Import your CSS file
import { useAuth } from '../../contexts/AuthContext';
import { Doughnut } from 'react-chartjs-2';
import {
  Chart as ChartJS,
  ArcElement,
  Tooltip,
  Legend
} from 'chart.js';

// Register required elements
ChartJS.register(ArcElement, Tooltip, Legend);

const ResultPage = ({ results, averageLevel }) => {
  const { user } = useAuth(); // Get the current user

  const clearBundleAndReload = () => {
    // Remove the item (bundle) from local storage
    localStorage.removeItem('bundle');
    window.location.reload();
  };

  const renderDoughnutChart = (title, data) => {
    if (!data) {
      return <p>N/A</p>;
    }

    const correctAnswers = data.correctAnswers;
    const totalQuestions = data.totalQuestions;
    const incorrectAnswers = totalQuestions - correctAnswers;

    const chartData = {
      datasets: [
        {
          data: [correctAnswers, incorrectAnswers],
          backgroundColor: ['#4caf50', '#f44336'],
          hoverBackgroundColor: ['#66bb6a', '#e57373'],
          borderWidth: 2,
          borderColor: '#ffffff',
        },
      ],
    };

    const options = {
      responsive: true,
      cutout: '50%', // Makes the chart look more modern and compact
      plugins: {
        legend: {
          position: 'bottom',
          labels: {
            font: {
              size: 12,
              family: 'Open Sans',
              weight: 'bold',
            },
            color: '#333',
            padding: 10,
          },
        },
        tooltip: {
          backgroundColor: '#f9f9f9',
          titleColor: '#333',
          bodyColor: '#555',
          borderColor: '#ddd',
          borderWidth: 1,
          displayColors: false,
          padding: 10,
          callbacks: {
            label: (tooltipItem) => {
              return `${tooltipItem.label}: ${tooltipItem.raw}`;
            },
          },
        },
      },
      maintainAspectRatio: false,
    };

    return (
      <div className="chart-container">
        <h4>{title}</h4>
        <div className="chart-wrapper">
          <Doughnut data={chartData} options={options} />
          <div className="chart-center-text" style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            textAlign: 'center',
            fontSize: '14px',
            fontWeight: 'bold',
            color: '#333'
          }}>
            {data.level}
          </div>
        </div>
      </div>
    );
  };

  const renderGeneralChart = () => {
    if (!results) {
      return <p>N/A</p>;
    }

    const sections = ['listening', 'reading', 'useOfeng'];
    const correctAnswers = sections.reduce((sum, section) => sum + (results[section]?.correctAnswers || 0), 0);
    const totalQuestions = sections.reduce((sum, section) => sum + (results[section]?.totalQuestions || 0), 0);
    const incorrectAnswers = totalQuestions - correctAnswers;

    const chartData = {
      datasets: [
        {
          data: [correctAnswers, incorrectAnswers],
          backgroundColor: ['#4caf50', '#f44336'],
          hoverBackgroundColor: ['#66bb6a', '#e57373'],
          borderWidth: 2,
          borderColor: '#ffffff',
        },
      ],
    };

    const options = {
      responsive: true,
      cutout: '50%',
      plugins: {
        legend: {
          position: 'bottom',
          labels: {
            font: {
              size: 12,
              family: 'Open Sans',
              weight: 'bold',
            },
            color: '#333',
            padding: 10,
          },
        },
        tooltip: {
          backgroundColor: '#f9f9f9',
          titleColor: '#333',
          bodyColor: '#555',
          borderColor: '#ddd',
          borderWidth: 1,
          displayColors: false,
          padding: 10,
          callbacks: {
            label: (tooltipItem) => {
              return `${tooltipItem.label}: ${tooltipItem.raw}`;
            },
          },
        },
      },
      maintainAspectRatio: false,
    };

    return (
      <div className="chart-container general-chart">
        <h4>General Test Results</h4>
        <div className="chart-wrapper">
          <Doughnut data={chartData} options={options} />
          <div className="chart-center-text" style={{
            position: 'absolute',
            top: '50%',
            left: '50%',
            transform: 'translate(-50%, -50%)',
            textAlign: 'center',
            fontSize: '14px',
            fontWeight: 'bold',
            color: '#333'
          }}>
            {averageLevel || 'N/A'}
          </div>
        </div>
      </div>
    );
  };

  return (
    <div className="result-page">
      <h2>Results</h2>
      <p>Welcome, {user?.displayName || 'Guest'}!</p>

      <div className="chart-row">
        <div className="result-section">
          {renderDoughnutChart('Listening', results.listening)}
        </div>

        <div className="result-section">
          {renderDoughnutChart('Reading', results.reading)}
        </div>

        <div className="result-section">
  {results.useOfeng ? (
    renderDoughnutChart('Use of English', results.useOfeng)
  ) : (
    <p>N/A</p>
  )}
</div>

      </div>

      <div className="general-result">
        {renderGeneralChart()}
      </div>

      
      <button onClick={clearBundleAndReload}>Try again</button>
    </div>
  );
};

export default ResultPage;
