// WorkbookPage.js

import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import '../../styles/CurrentLesson.css';
import '../../styles/LessonPage.css';
import { useLearning } from '../../contexts/LearningContext';
import { useAuth } from '../../contexts/AuthContext';
import parse from 'html-react-parser';

import WorkbookHeader from './WorkbookHeader';
import WorkbookNavigationL from './WorkbookNavigationL';
import WorkbookNavigationR from './WorkbookNavigationR';
import RenderWorkbookParts from './RenderWorkbookParts';
import WorkbookSummary from './WorkbookSummary';
import ProgressBar from './ProgressBar';
import HelperButton from './WorkbookHelperz';

const WorkbookPage = () => {
  const { user } = useAuth();
  const { workbookID } = useParams(); // expects a "workbookID" param from the route
  const navigate = useNavigate();

  const [workbook, setWorkbook] = useState(null);
  const [error, setError] = useState(null);
  const [finishError, setFinishError] = useState(null);
  const [currentPartIndex, setCurrentPartIndex] = useState(0);
  const [currentPartKeys, setCurrentPartKeys] = useState([]);
  const [currentPartName, setCurrentPartName] = useState('');
  const [startTime, setStartTime] = useState(null);
  const [correctAnswers, setCorrectAnswers] = useState(0);
  const [totalQuestions, setTotalQuestions] = useState(0);
  const [totalParts, setTotalParts] = useState(0);
  const [showSummary, setShowSummary] = useState(false);

  const {
    selectedCourse,
    finishWorkbook, // note the change from finishLesson -> finishWorkbook
    userInteractions,
    setUserInteractions,
    updateUserInteraction,
    setCurrentLessonOrWorkbookID,
  } = useLearning();

  // 1) Calculate total input-based questions (for stats)
  const calculateTotalQuestions = (workbookData) => {
    const total = Object.keys(workbookData).filter((key) => key.includes('input')).length;
    setTotalQuestions(total);
  };

  // 2) Flatten the course modules to find workbook pages
  const getFlattenedWorkbooks = (course) => {
    if (!course || !Array.isArray(course.modules)) return [];
    return course.modules.flatMap((module) => module.workbooks || []);
  };

  // 3) Update the "Part" name
  const updateCurrentPartName = (workbookData, index) => {
    const partKey = `Part${index + 1}/Type`;
    const partName = workbookData[partKey] || `Part ${index + 1}`;
    setCurrentPartName(partName);
  };

  // 4) Load the targeted workbook from the flattened array
  const loadWorkbook = (workbooks, targetWorkbookID) => {
    const workbookData =
      workbooks.find((w) => w.id === targetWorkbookID) ||
      workbooks.find((w) => !w.completed) ||
      workbooks[0];

    if (workbookData) {
      setWorkbook(workbookData);
      calculateTotalQuestions(workbookData);
      updateCurrentPartName(workbookData, 0);
      setStartTime(Date.now());
      setUserInteractions(workbookData.userInteractions || {});
      setCurrentLessonOrWorkbookID(`workbook/${workbookData.moduleID}/${workbookData.id}`);
    } else {
      setError('Workbook not found');
    }
  };

  // 5) On mount or if selectedCourse/workbookID changes, load the workbook
  useEffect(() => {
    if (selectedCourse) {
      const flattenedWorkbooks = getFlattenedWorkbooks(selectedCourse);
      loadWorkbook(flattenedWorkbooks, workbookID);
    } else {
      setError('Selected course or modules data not available');
    }
  }, [selectedCourse, workbookID]);

  // 6) When the workbook or currentPartIndex changes, update part name
  useEffect(() => {
    if (workbook) {
      updateCurrentPartName(workbook, currentPartIndex);
    }
  }, [workbook, currentPartIndex]);

  // 7) Count correct answers from userInteractions
  useEffect(() => {
    const correctCount = Object.values(userInteractions).filter(
      (interaction) => interaction.correct
    ).length;
    setCorrectAnswers(correctCount);
  }, [userInteractions]);

  // 8) Compute total number of parts & update `currentPartKeys`
  useEffect(() => {
    if (!workbook) return;

    // A) Find total number of parts by finding keys such as "PartX/Type"
    const parts = Object.keys(workbook)
      .filter((key) => key.startsWith('Part') && key.includes('/Type'))
      .map((key) => parseInt(key.match(/Part(\d+)/)[1], 10))
      .filter((num) => !isNaN(num));

    const maxPart = parts.length > 0 ? Math.max(...parts) : 0;
    setTotalParts(maxPart);

    // B) Build an array of possible subkeys for the current part
    const currentPartPrefix = `Part${currentPartIndex + 1}`;
    const partOrder = [
      `${currentPartPrefix}/A.Warm-up`,
      `${currentPartPrefix}/A.task`,
      `${currentPartPrefix}/A.kit`,
      `${currentPartPrefix}/A.answer`,
      `${currentPartPrefix}/A.pic`,
      `${currentPartPrefix}/A.article`,
      `${currentPartPrefix}/A.articleAnswers`,
      `${currentPartPrefix}/A.audio`,
      `${currentPartPrefix}/A.hint`,
      `${currentPartPrefix}/A.transcription`,
      `${currentPartPrefix}/B.task`,
      `${currentPartPrefix}/B.kit`,
      `${currentPartPrefix}/B.answer`,
      `${currentPartPrefix}/C.task`,
      `${currentPartPrefix}/C.kit`,
      `${currentPartPrefix}/C.answers`,
      `${currentPartPrefix}/D.task`,
      `${currentPartPrefix}/D.kit`,
      `${currentPartPrefix}/D.answer`,
    ];

    // Filter out the subkeys that exist in the workbook data
    const foundKeys = partOrder.filter((key) => workbook[key]);
    setCurrentPartKeys(foundKeys);

    // C) If no content for this part, auto-advance or show summary
    if (foundKeys.length === 0) {
      if (currentPartIndex + 1 < maxPart) {
        setCurrentPartIndex(currentPartIndex + 1);
      } else {
        setShowSummary(true);
      }
    }
  }, [workbook, currentPartIndex]);

  // 9) Navigation handlers
  const handleNext = () => {
    const newIndex = currentPartIndex + 1;
    if (newIndex < totalParts) {
      setCurrentPartIndex(newIndex);
    } else {
      setShowSummary(true);
    }
  };

  const handlePrevious = () => {
    if (showSummary) {
      setShowSummary(false);
      setCurrentPartIndex(totalParts - 1);
    } else {
      const newIndex = currentPartIndex - 1;
      if (newIndex >= 0) {
        setCurrentPartIndex(newIndex);
      }
    }
  };

  // 10) Finish workbook handler
  const handleFinishWorkbook = async () => {
    try {
      const endTime = Date.now();
      const timeSpent = Math.round((endTime - startTime) / 1000);

      const workbookStats = {
        timeSpent,
        correctAnswers,
        totalQuestions,
      };

      await finishWorkbook(`${workbook.moduleID}/${workbook.id}`, workbookStats);
      navigate('/lesson'); // update the navigation path as needed
    } catch (error) {
      setFinishError('Failed to finish the workbook. Please try again.');
    }
  };

  // 11) Display an error message if the workbook data is missing
  if (error) {
    return <div>{error}</div>;
  }
  if (!workbook) {
    return <div>Workbook data is missing</div>;
  }

  // 12) Render the workbook page
  return (
    <div>
      <WorkbookHeader currentPartName={currentPartName} workbook={workbook} />

      <div className="lesson-page">
        <WorkbookNavigationL
          currentPartIndex={currentPartIndex}
          handlePrevious={handlePrevious}
        />

        {showSummary ? (
          <WorkbookSummary
            startTime={startTime}
            correctAnswers={correctAnswers}
            totalQuestions={totalQuestions}
            handleFinishWorkbook={handleFinishWorkbook}
          />
        ) : (
          <RenderWorkbookParts
            currentPartIndex={currentPartIndex}
            workbook={workbook}
            userInteractions={userInteractions}
            updateUserInteraction={updateUserInteraction}
          />
        )}

        <WorkbookNavigationR
          currentPartIndex={currentPartIndex}
          handleNext={handleNext}
        />
      </div>

      {finishError && <div className="error">{finishError}</div>}

      {/* If we want the summary to be an extra step in the progress bar, pass adjusted props */}
      <ProgressBar
        currentPartIndex={showSummary ? totalParts : currentPartIndex}
        totalParts={totalParts + 1}
      />

      {/* HelperButton now receives the current part keys to parse only those keys for correct answers */}
      <HelperButton
        workbook={workbook}
        user={user}
        currentPartKeys={currentPartKeys}
      />
    </div>
  );
};

export default WorkbookPage;
