import React, { useState, useEffect, useCallback } from 'react';
import DOMPurify from 'dompurify';
import parse from 'html-react-parser';

import helperz from '../../assets/helperz_transp.png';
import checkIcon from '../../assets/lessonTool/toolCheck.svg';
import grammarIcon from '../../assets/lessonTool/toolGrammar.svg';
import wordlistIcon from '../../assets/lessonTool/toolWordlist.svg';

/**
 * Props:
 *  - lesson: The lesson object
 *  - user:   The current user object (for nativeLanguage)
 *  - currentPartKeys: (optional) array of lesson keys corresponding to
 *    the *currently displayed part* if you only want to show answers for that part
 */
const HelperButton = ({ workbook, user, currentPartKeys = [] }) => {
  const [showButtons, setShowButtons] = useState(false);
  const [hasHoveredOnce, setHasHoveredOnce] = useState(false);
  const [currentTipIndex, setCurrentTipIndex] = useState(0);
  const [overlayContent, setOverlayContent] = useState(null);

  // Tips for the main button hover
  const tips = [
    "Hi, I'm Anya! Your study buddy :)",
    "Visit 'Vault' to get more!",
    "Take short breaks to improve focus.",
    "Review WORD LIST daily.",
  ];

  useEffect(() => {
    if (!workbook) {
      console.error('HelperButton: workbook data is missing');
    }
  }, [workbook]);

  /**
   * Gathers correct answers by scanning `data-correct` in the HTML
   * for *only* the keys in currentPartKeys (i.e., the currently rendered part).
   * If you want *all* keys in the lesson, replace `currentPartKeys` with something else.
   */
  const gatherCorrectAnswers = useCallback(() => {
    if (!workbook || currentPartKeys.length === 0) return [];

    const correctAnswers = [];

    currentPartKeys.forEach((key) => {
      const htmlContent = workbook[key];
      if (!htmlContent) return;

      const answersForKey = new Set(); // avoid duplicates

      // Recursive function to find data-correct
      const findDataCorrect = (domNode) => {
        if (domNode?.attribs?.['data-correct']) {
          answersForKey.add(domNode.attribs['data-correct']);
        }
        if (domNode?.children?.length) {
          domNode.children.forEach((child) => findDataCorrect(child));
        }
      };

      // Sanitize and parse the HTML to detect data-correct
      const sanitizedContent = DOMPurify.sanitize(htmlContent);
      parse(sanitizedContent, {
        replace: (domNode) => {
          findDataCorrect(domNode);
        },
      });

      // Only add if we found any unique answers
      if (answersForKey.size > 0) {
        correctAnswers.push({
          part: key,
          answers: Array.from(answersForKey),
        });
      }
    });

    return correctAnswers;
  }, [workbook, currentPartKeys]);

  // Hover logic for the main (Anya) button
  const handleHover = () => {
    if (!hasHoveredOnce) {
      setHasHoveredOnce(true);
    } else {
      setCurrentTipIndex((prevIndex) => (prevIndex + 1) % (tips.length - 1) + 1);
    }
  };

  /**
   * Render correct answers overlay
   */
  const renderCorrectAnswers = () => {
    const data = gatherCorrectAnswers();
    if (!data || data.length === 0) {
      return <p>No correct answers for this part.</p>;
    }

    return (
      <div>
        <h2>Correct Answers for Current Part</h2>
        <ol>
          {data.map((item, index) => (
            <li key={index}>
              <strong>{item.part}</strong>
              <ol>
                {item.answers.map((ans, ansIndex) => (
                  <li key={ansIndex}>{ans}</li>
                ))}
              </ol>
            </li>
          ))}
        </ol>
      </div>
    );
  };

  /**
   * Render Grammar Bank
   */
  const renderGrammarBank = () => {
    if (!workbook?.GRAMMARBANK) {
      return <p>No grammar content available.</p>;
    }
    return (
      <div>
        <h2>Grammar Bank</h2>
        <div
          dangerouslySetInnerHTML={{
            __html: DOMPurify.sanitize(workbook.GRAMMARBANK),
          }}
        />
      </div>
    );
  };

  /**
   * Render Wordlist from JSON or array, showing only the users native language
   */
  const renderWordlist = () => {
    if (!workbook?.WORDLIST) {
      return <p>No wordlist available.</p>;
    }

    let wordlistArr = [];
    if (typeof workbook.WORDLIST === 'string') {
      try {
        wordlistArr = JSON.parse(workbook.WORDLIST);
      } catch (err) {
        console.error('Error parsing WORDLIST JSON:', err);
        return <p>Could not parse wordlist data.</p>;
      }
    } else if (Array.isArray(workbook.WORDLIST)) {
      wordlistArr = workbook.WORDLIST;
    }

    if (!Array.isArray(wordlistArr)) {
      return <p>No valid wordlist array found.</p>;
    }

    const nativeLang = user?.nativeLanguage || null;

    return (
      <div>
        <h2>Wordlist</h2>
        <ul>
          {wordlistArr.map((item, index) => {
            let userTranslation = null;
            if (item.translations && nativeLang && item.translations[nativeLang]) {
              userTranslation = item.translations[nativeLang];
            }
            return (
              <li key={index}>
                <div className="wordlist-item" style={{ marginBottom: '1rem' }}>
                  <div style={{ margin: '0.4rem 0' }}>
                    <strong>{item.word}</strong> <em>({item.partOfSpeech})</em>
                    <br />- {item.elementaryDefinition}
                  </div>

                  {userTranslation ? (
                    <div>
                      <span className="translation">{userTranslation}</span>
                    </div>
                  ) : (
                    <div style={{ fontStyle: 'italic' }}>
                      Translation not available for your native language.
                    </div>
                  )}

                  {item.example && (
                    <div style={{ fontStyle: 'oblique', background: 'antiquewhite', padding: '0.2rem' }}>
                      {item.example}
                    </div>
                  )}
                </div>
              </li>
            );
          })}
        </ul>
      </div>
    );
  };

  /**
   * Decide which content to show in the overlay
   */
  const handleOverlay = (type) => {
    switch (type) {
      case 'Correct Answers':
        setOverlayContent(renderCorrectAnswers());
        break;
      case 'Grammar Bank':
        setOverlayContent(renderGrammarBank());
        break;
      case 'Wordlist':
        setOverlayContent(renderWordlist());
        break;
      default:
        setOverlayContent(null);
    }
  };

  return (
    <div className="helperz">
      {/* Main "Anya" button */}
      <div
        className={`main-button ${showButtons ? 'active' : ''}`}
        onClick={() => setShowButtons(!showButtons)}
        onMouseEnter={handleHover}
      >
        <img src={helperz} className="helperzImg" alt="Helper Menu" />
        <span className="hover-text">
          {hasHoveredOnce ? tips[currentTipIndex] : tips[0]}
        </span>
      </div>

      {/* Secondary floating buttons */}
      {showButtons && (
        <div className="fixed-button">
          <div className="fixed" onClick={() => handleOverlay('Correct Answers')}>
            <img className="logo" src={checkIcon} alt="checkAnswers" />
            <span className="tipper">INSPECTION</span>
          </div>
          <div className="fixed" onClick={() => handleOverlay('Grammar Bank')}>
            <img className="logo" src={grammarIcon} alt="grammarbank" />
            <span className="tipper">GRAMMAR BANK</span>
          </div>
          <div className="fixed" onClick={() => handleOverlay('Wordlist')}>
            <img className="logo" src={wordlistIcon} alt="wordlist" />
            <span className="tipper">WORDLIST</span>
          </div>
        </div>
      )}

      {/* Overlay area */}
      {overlayContent && (
        <div className="overlay">
          <div className="lesson-overlay-data">
            <button className="close-button" onClick={() => setOverlayContent(null)}>
              Close
            </button>
            {overlayContent}
          </div>
        </div>
      )}
    </div>
  );
};

export default HelperButton;
