// LessonPage.js

import React, { useEffect, useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import '../../styles/CurrentLesson.css';
import '../../styles/LessonPage.css';
import { useLearning } from '../../contexts/LearningContext';
import { useAuth } from '../../contexts/AuthContext';
import parse from 'html-react-parser';

import LessonHeader from './LessonHeader';
import LessonNavigationL from './LessonNavigationL';
import LessonNavigationR from './LessonNavigationR';
import RenderLessonParts from './RenderLessonParts';
import LessonSummary from './LessonSummary';
import ProgressBar from './ProgressBar';
import HelperButton from './helperz';

const LessonPage = () => {
  const { user } = useAuth();
  const { lessonID } = useParams();
  const navigate = useNavigate();

  const [lesson, setLesson] = useState(null);
  const [error, setError] = useState(null);
  const [finishError, setFinishError] = useState(null);
  const [currentPartIndex, setCurrentPartIndex] = useState(0);
  const [currentPartKeys, setCurrentPartKeys] = useState([]);
  const [currentPartName, setCurrentPartName] = useState('');
  const [startTime, setStartTime] = useState(null);
  const [correctAnswers, setCorrectAnswers] = useState(0);
  const [totalQuestions, setTotalQuestions] = useState(0);
  const [totalParts, setTotalParts] = useState(0);
  const [showSummary, setShowSummary] = useState(false);

  const {
    selectedCourse,
    finishLesson,
    userInteractions,
    setUserInteractions,
    updateUserInteraction,
    setCurrentLessonOrWorkbookID,
  } = useLearning();

  // 1) Calculate total input-based questions (for stats)
  const calculateTotalQuestions = (lesson) => {
    const total = Object.keys(lesson).filter((key) => key.includes('input')).length;
    setTotalQuestions(total);
  };

  // 2) Flatten the course modules to find lessons
  const getFlattenedLessons = (course) => {
    if (!course || !Array.isArray(course.modules)) return [];
    return course.modules.flatMap((module) => module.lessons || []);
  };

  // 3) Update the "Part" name
  const updateCurrentPartName = (lesson, index) => {
    const partKey = `Part${index + 1}/Type`;
    const partName = lesson[partKey] || `Part ${index + 1}`;
    setCurrentPartName(partName);
  };

  // 4) Load the targeted lesson from the flattened array
  const loadLesson = (lessons, targetLessonID) => {
    const lessonData =
      lessons.find((l) => l.id === targetLessonID) ||
      lessons.find((l) => !l.completed) ||
      lessons[0];

    if (lessonData) {
      setLesson(lessonData);
      calculateTotalQuestions(lessonData);
      updateCurrentPartName(lessonData, 0);
      setStartTime(Date.now());
      setUserInteractions(lessonData.userInteractions || {});
      setCurrentLessonOrWorkbookID(`lesson/${lessonData.moduleID}/${lessonData.id}`);
    } else {
      setError('Lesson not found');
    }
  };

  // 5) On mount or if selectedCourse/lessonID changes, load the lesson
  useEffect(() => {
    if (selectedCourse) {
      const flattenedLessons = getFlattenedLessons(selectedCourse);
      loadLesson(flattenedLessons, lessonID);
    } else {
      setError('Selected course or modules data not available');
    }
  }, [selectedCourse, lessonID]);

  // 6) When the lesson or currentPartIndex changes, update part name
  useEffect(() => {
    if (lesson) {
      updateCurrentPartName(lesson, currentPartIndex);
    }
  }, [lesson, currentPartIndex]);

  // 7) Count correct answers from userInteractions
  useEffect(() => {
    const correctCount = Object.values(userInteractions).filter((interaction) => interaction.correct)
      .length;
    setCorrectAnswers(correctCount);
  }, [userInteractions]);

  // 8) Compute total number of parts & update `currentPartKeys`
  useEffect(() => {
    if (!lesson) return;

    // A) Find total number of parts
    const parts = Object.keys(lesson)
      .filter((key) => key.startsWith('Part') && key.includes('/Type'))
      .map((key) => parseInt(key.match(/Part(\d+)/)[1], 10))
      .filter((num) => !isNaN(num));

    const maxPart = parts.length > 0 ? Math.max(...parts) : 0;
    setTotalParts(maxPart);

    // B) Build an array of possible subkeys for the current part
    const currentPartPrefix = `Part${currentPartIndex + 1}`;
    const partOrder = [
      `${currentPartPrefix}/A.Warm-up`,
      `${currentPartPrefix}/A.task`,
      `${currentPartPrefix}/A.kit`,
      `${currentPartPrefix}/A.answer`,
      `${currentPartPrefix}/A.pic`,
      `${currentPartPrefix}/A.article`,
      `${currentPartPrefix}/A.articleAnswers`,
      `${currentPartPrefix}/A.audio`,
      `${currentPartPrefix}/A.hint`,
      `${currentPartPrefix}/A.transcription`,
      `${currentPartPrefix}/B.task`,
      `${currentPartPrefix}/B.kit`,
      `${currentPartPrefix}/B.answer`,
      `${currentPartPrefix}/C.task`,
      `${currentPartPrefix}/C.kit`,
      `${currentPartPrefix}/C.answers`,
      `${currentPartPrefix}/D.task`,
      `${currentPartPrefix}/D.kit`,
      `${currentPartPrefix}/D.answer`,
    ];

    // Filter out the subkeys that actually exist in the lesson
    const foundKeys = partOrder.filter((key) => lesson[key]);
    setCurrentPartKeys(foundKeys);

    // C) If no content for this part, auto-advance or show summary
    if (foundKeys.length === 0) {
      if (currentPartIndex + 1 < maxPart) {
        setCurrentPartIndex(currentPartIndex + 1);
      } else {
        setShowSummary(true);
      }
    }
  }, [lesson, currentPartIndex]);

  // 9) Navigation handlers
  const handleNext = () => {
    const newIndex = currentPartIndex + 1;
    if (newIndex < totalParts) {
      setCurrentPartIndex(newIndex);
    } else {
      setShowSummary(true);
    }
  };

  const handlePrevious = () => {
    if (showSummary) {
      setShowSummary(false);
      setCurrentPartIndex(totalParts - 1);
    } else {
      const newIndex = currentPartIndex - 1;
      if (newIndex >= 0) {
        setCurrentPartIndex(newIndex);
      }
    }
  };

  // 10) Finish lesson handler
  const handleFinishLesson = async () => {
    try {
      const endTime = Date.now();
      const timeSpent = Math.round((endTime - startTime) / 1000);

      const lessonStats = {
        timeSpent,
        correctAnswers,
        totalQuestions,
      };

      await finishLesson(`${lesson.moduleID}/${lesson.id}`, lessonStats);
      navigate('/lesson'); // or wherever you want to go next
    } catch (error) {
      setFinishError('Failed to finish the lesson. Please try again.');
    }
  };

  // 11) If we have an error or no lesson
  if (error) {
    return <div>{error}</div>;
  }
  if (!lesson) {
    return <div>Lesson data is missing</div>;
  }

  // 12) Render
  return (
    <div>
      <LessonHeader currentPartName={currentPartName} lesson={lesson} />

      <div className="lesson-page">
        <LessonNavigationL
          currentPartIndex={currentPartIndex}
          handlePrevious={handlePrevious}
        />

        {showSummary ? (
          <LessonSummary
            startTime={startTime}
            correctAnswers={correctAnswers}
            totalQuestions={totalQuestions}
            handleFinishLesson={handleFinishLesson}
          />
        ) : (
          <RenderLessonParts
  currentPartIndex={currentPartIndex}
  lesson={lesson}
  userInteractions={userInteractions}
  updateUserInteraction={updateUserInteraction}
/>
        )}

        <LessonNavigationR
          currentPartIndex={currentPartIndex}
          handleNext={handleNext}
        />
      </div>

      {finishError && <div className="error">{finishError}</div>}

      {/* If we want the summary to be an extra step in the progress bar, we pass adjusted props. */}
      <ProgressBar
        currentPartIndex={showSummary ? totalParts : currentPartIndex}
        totalParts={totalParts + 1}
      />

      {/* Now, pass the current part keys to HelperButton so it only parses *those* keys for correct answers */}
      <HelperButton
        lesson={lesson}
        user={user}
        currentPartKeys={currentPartKeys}  // <--- KEY change
      />
    </div>
  );
};

export default LessonPage;
