import React, { useState, useEffect } from 'react';
import { useAuth } from '../contexts/AuthContext';
import fetchAndUploadData from './admin/fetchAndUpload';
import UserManagement from './admin/userManagement';
import { db } from '../firebase'; // Firebase configuration
import '../styles/AdminPage.css';

const AdminPage = () => {
  const { user } = useAuth();
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);
  const [isAdmin, setIsAdmin] = useState(false);
  const [users, setUsers] = useState([]); // State for fetched user data
  const [stats, setStats] = useState({
    totalUsers: 0,
    activeUsers: 0,
    inactiveUsers: 0,
  }); // User stats

  useEffect(() => {
    if (user) {
      setIsAdmin(user.role === 'admin'); // Check if the user is an admin
    }
  }, [user]);

  // Fetch Users from Firebase
  useEffect(() => {
    if (isAdmin) {
      const fetchUsers = async () => {
        try {
          const usersRef = db.collection('users'); // Firestore reference
          const snapshot = await usersRef.get();
          const usersList = snapshot.docs.map((doc) => ({
            id: doc.id,
            ...doc.data(),
          }));
          setUsers(usersList);

          // Update stats
          const total = usersList.length;
          const active = usersList.filter((user) => user.status === 'active').length;
          const inactive = total - active;
          setStats({ totalUsers: total, activeUsers: active, inactiveUsers: inactive });
        } catch (err) {
          console.error('Error fetching users:', err);
          setError('Failed to fetch users.');
        }
      };
      fetchUsers();
    }
  }, [isAdmin]);

  // Fetch and Upload Data
  const handleFetchAndUploadData = () => {
    fetchAndUploadData(isAdmin, setLoading, setError);
  };

  return (
    <div className="admin-container">
      
            
          

      {/* Main Content */}
      <main className="main-content">
        {isAdmin ? (
          <>

            {/* User Management */}
            <div className="user-management">
              <UserManagement users={users} />
              <button onClick={handleFetchAndUploadData} disabled={loading}>
              {loading ? 'Uploading...' : 'Fetch and Upload Data'}
            </button>
            </div>
          </>
        ) : (
          <p>You do not have permission to access this page.</p>
        )}
        {error && <p style={{ color: 'red' }}>{error}</p>}
      </main>
    </div>
  );
};

export default AdminPage;
