import React from 'react';
import '../../styles/CourseOverlay.css';

const CourseOverlay = ({ course, onClose }) => {
  return (
    <div className="course-overlay-backdrop">
      <div className="course-overlay-content">
        
        {/* Close Button */}
        <button className="course-close-button" onClick={onClose}></button>

        {/* Title and Description */}
        <div className="overlay-header">
          <h2>{course.title}</h2>
          <p>{course.description}</p>
        </div>

        {/* Extended Details */}
        <div className="overlay-details">
          {course.detailedDescription && (
            <section className="details-section">
              <h3>About This Level</h3>
              <p>{course.detailedDescription}</p>
            </section>
          )}

          {course.whoIsItFor && (
            <section className="details-section">
              <h3>Who Is It For?</h3>
              <p>{course.whoIsItFor}</p>
            </section>
          )}
          
          {course.skillsGained && course.skillsGained.length > 0 && (
            <section className="details-section">
              <h3>Skills You Will Gain</h3>
              <ul>
                {course.skillsGained.map((skill, index) => (
                  <li key={index}>{skill}</li>
                ))}
              </ul>
            </section>
          )}

          <p><strong>Modules:</strong> {course.modulesCount}</p>
          <p><strong>Lessons per Module:</strong> {course.totalLessonsPerModule}</p>
        </div>

        {/* CTA Buttons */}
        <div className="overlay-cta">
          <button className="enroll-button">Enroll Now</button>
        </div>
      </div>
    </div>
  );
};

export default CourseOverlay;
