import React, { useState, useEffect } from 'react';

const Reading = ({ nextPart, onResultsCalculated  }) => {
  const passage = `
    In recent years, the rise of social media platforms has significantly transformed the way people communicate. 
    Platforms like Facebook, Twitter, Instagram, and LinkedIn have provided users with new ways to interact, 
    share information, and stay connected with others around the globe. These changes have had profound effects 
    on personal relationships, professional networking, and even political activism.@

    One of the most significant impacts of social media is the way it has changed interpersonal communication. 
    Traditionally, communication was limited to face-to-face interactions, phone calls, and written correspondence. 
    However, social media has introduced new forms of communication, such as instant messaging, video calls, and public posts. 
    These platforms allow people to maintain relationships over long distances and stay in touch with a broader network of acquaintances.
@ 
    Social media has also revolutionized the professional world. Networking sites like LinkedIn have become essential 
    tools for job seekers and employers alike. Professionals can connect with others in their industry, share their work, 
    and even find new job opportunities. This has made the job market more competitive and dynamic, with individuals 
    needing to constantly update their profiles and skills to stay relevant.
@ 
    However, the rise of social media has not been without its drawbacks. Critics argue that it has led to a decline in 
    face-to-face communication and has contributed to the spread of misinformation. Additionally, the constant use of 
    social media can lead to issues such as anxiety, depression, and addiction. The curated nature of social media content, 
    where users often only share the highlights of their lives, can create unrealistic expectations and feelings of inadequacy among viewers.
@ 
    Moreover, social media has played a significant role in political activism and public discourse. Movements like #MeToo 
    and Black Lives Matter have gained momentum through social media, allowing activists to reach a global audience quickly 
    and efficiently. However, this has also led to the spread of extremist views and the manipulation of public opinion through 
    targeted misinformation campaigns.
@ 
    In conclusion, social media has dramatically altered the landscape of communication, with both positive and negative consequences. 
    While it has made it easier to stay connected and share information, it has also introduced new challenges, such as the decline 
    in personal interaction and the rise of misinformation. As social media continues to evolve, it will be important to find a balance 
    that maximizes its benefits while minimizing its negative impacts.
  `;

  const paragraphs = passage.trim().split('@');
  const [userAnswers, setUserAnswers] = useState({});
  const [showResults, setShowResults] = useState(false);

  const questions = [
    // Beginner Level
    {
      id: 1,
      type: 'multiple-choice',
      question: 'What is the passage mainly about?',
      options: [
        'a. The history of social media.',
        'b. The impact of social media on communication.',
        'c. How to use social media.',
        'd. The negative effects of social media.'
      ],
      correctAnswer: 'b. The impact of social media on communication.'
    },
    {
      id: 2,
      type: 'multiple-choice',
      question: 'According to the passage, which platform is used for professional networking?',
      options: [
        'a. Instagram',
        'b. Twitter',
        'c. LinkedIn',
        'd. Facebook'
      ],
      correctAnswer: 'c. LinkedIn'
    },
    {
      id: 3,
      type: 'multiple-choice',
      question: 'What does "maintain relationships" mean in the passage?',
      options: [
        'a. To end relationships.',
        'b. To make new friends.',
        'c. To keep relationships.',
        'd. To meet new people.'
      ],
      correctAnswer: 'c. To keep relationships.'
    },
    {
      id: 4,
      type: 'multiple-choice',
      question: 'Which of the following is one negative impact of social media, as mentioned in the passage?',
      options: [
        'a. It has made face-to-face communication more common.',
        'b. It has reduced written communication.',
        'c. It has contributed to the spread of misinformation.',
        'd. It has made people healthier.'
      ],
      correctAnswer: 'c. It has contributed to the spread of misinformation.'
    },
    {
      id: 5,
      type: 'multiple-choice',
      question: 'What does the phrase "curated nature of social media content" suggest?',
      options: [
        'a. Content is selected and presented in a controlled way.',
        'b. Content is completely random.',
        'c. Content is harmful to users.',
        'd. Content is always true.'
      ],
      correctAnswer: 'a. Content is selected and presented in a controlled way.'
    },
    {
      id: 6,
      type: 'multiple-choice',
      question: 'What is one way social media has impacted political activism, according to the passage?',
      options: [
        'a. It has stopped political movements.',
        'b. It has slowed down political movements.',
        'c. It has helped movements reach more people.',
        'd. It has replaced traditional political campaigns.'
      ],
      correctAnswer: 'c. It has helped movements reach more people.'
    },
    {
      id: 7,
      type: 'multiple-choice',
      question: 'The author mentions #MeToo and Black Lives Matter as examples to illustrate which point?',
      options: [
        'a. Social media has weakened political movements.',
        'b. Social media has made political activism more effective.',
        'c. Social media has reduced the importance of political activism.',
        'd. Social media is not used for political purposes.'
      ],
      correctAnswer: 'b. Social media has made political activism more effective.'
    },
    {
      id: 8,
      type: 'multiple-choice',
      question: 'In the passage, what does the term "dynamic" in the context of the job market mean?',
      options: [
        'a. The job market is becoming more predictable.',
        'b. The job market is changing and evolving quickly.',
        'c. The job market is becoming less competitive.',
        'd. The job market is stable and fixed.'
      ],
      correctAnswer: 'b. The job market is changing and evolving quickly.'
    },
    {
      id: 9,
      type: 'multiple-choice',
      question: 'Which of the following is an inference you can make from the passage?',
      options: [
        'a. Social media is primarily harmful and should be avoided.',
        'b. Social media can have both positive and negative effects on society.',
        'c. Social media will replace all forms of traditional communication.',
        'd. Social media is only beneficial for professional networking.'
      ],
      correctAnswer: 'b. Social media can have both positive and negative effects on society.'
    },
    {
      id: 10,
      type: 'multiple-choice',
      question: 'What is the authors tone in the passage when discussing the future of social media?',
      options: [
        'a. Optimistic about its complete success.',
        'b. Critical and dismissive of its usefulness.',
        'c. Cautiously optimistic with awareness of its challenges.',
        'd. Neutral with no strong opinion.'
      ],
      correctAnswer: 'c. Cautiously optimistic with awareness of its challenges.'
    }
  ];
  
  useEffect(() => {
    // Load answers from local storage when the component mounts
    const storedAnswers = JSON.parse(localStorage.getItem('readingAnswers')) || {};
    setUserAnswers(storedAnswers);
  }, []);

  const handleAnswer = (questionId, selectedOption) => {
    const updatedAnswers = {
      ...userAnswers,
      [questionId]: selectedOption,
    };

    setUserAnswers(updatedAnswers);
    // Save updated answers to local storage
    localStorage.setItem('readingAnswers', JSON.stringify(updatedAnswers));
  };

  const renderQuestion = (question) => (
    <li key={question.id} className="question-block">
      <p>{question.question}</p>
      {question.options.map((option, index) => (
        <><button
          key={index}
          style={{
            backgroundColor: userAnswers[question.id] === option ? '#94FFD8' : '#f0f0f0',  // Change color if selected
            color: userAnswers[question.id] === option ? '#000' : '#333',  // Change text color if selected
            cursor: 'pointer',
            border: '1px solid #ccc',
            padding: '10px',
            margin: '5px'
          }}
          onClick={() => handleAnswer(question.id, option)}
          disabled={userAnswers[question.id] !== undefined} // Disable after answering
          className={userAnswers[question.id] === option ? 'selected' : ''}
        >
          {option}
        </button> <br /></>
      ))}
    </li>
  );

  const calculateResults = () => {
    const storedAnswers = JSON.parse(localStorage.getItem('readingAnswers')) || {};
    const validAnswers = questions.filter(q => q.id in storedAnswers);
    const totalQuestions = 10;
    const correctAnswers = validAnswers.filter(q => storedAnswers[q.id] === q.correctAnswer).length;

    const level = getLevel(correctAnswers, totalQuestions);
    
    // Call the parent function to handle results
    onResultsCalculated({ reading: { totalQuestions, correctAnswers, level } });
    nextPart(); // Move to the next part after calculating results
  };

  // New function to determine level
  const getLevel = (correct, total) => {
    const percentage = (correct / total) * 100;
    if (percentage >= 95) return 'B2';
    if (percentage >= 75) return 'B2'; // 6-8 correct
    if (percentage >= 50) return 'B1'; // 4-5 correct
    if (percentage >= 25) return 'A2'; // 2-3 correct
    return 'A1'; // 0-1 correct
  };

  return (
    
    <div className="reading-use-of-english-grid">
      <div className="introTest break">
      <h2>READING</h2>
      <h4>Read the following text and answer the questions 1-10. You can select an answer only once.</h4>
    </div>
      <div className="passage overbox">
        <h3>DOES SOCIAL MEDIA REVOLUTIONISE CONTEMPORARY LIVING?</h3>
        {paragraphs.map((paragraph, index) => (
          <p key={index}>{paragraph}</p>
        ))}
      </div>
      <div  className="questions overbox">
      <ol>
          <>
            {questions.map(question => renderQuestion(question))}
            <button className="centralized" onClick={calculateResults}> NEXT</button>
          </>
        
      </ol>
    </div></div>
  );
};

export default Reading;
