// src/components/Vault/VaultPage.js

import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useLearning } from '../contexts/LearningContext';
import { useAuth } from '../contexts/AuthContext';

import '../styles/VaultGeneral.css';
import '../styles/VaultIrregularVerbs.css';
import '../styles/VaultWordlist.css';
import '../styles/VaultWordlistOverview.css';

import errorImg from '../pics/vaulterror.webp';

const VaultPage = () => {
  const navigate = useNavigate();
  const { selectedCourse, loading, error } = useLearning();
  const { user } = useAuth();
  const [activeItem, setActiveItem] = useState(null);

  // Define your vault items with their title, brief description,
  // and either an overlayContent or a direct onClick action.
  const vaultItems = [
    {
      id: 'verbs',
      title: 'Irregular Verbs Trainer',
      description: 'Practice irregular verbs for multiple levels.',
      overlayContent: (
        <div className="overlay-content">
          <h3>Irregular Verbs Trainer</h3>
          <p>
            Practice irregular verbs for various levels. Click on a level to begin training.
          </p>
          <div className="buttons-container">
            {[
              { name: 'A0', path: '/vault/a0' },
              { name: 'A1', path: '/vault/a1' },
              { name: 'A2', path: '/vault/a2' },
              { name: 'B1', path: '/vault/b1' },
              { name: 'Advanced', path: '/vault/advanced' },
            ].map((level) => (
              <button
                key={level.name}
                onClick={() => {
                  setActiveItem(null); // Close the overlay
                  navigate(level.path);
                }}
                className="level-button"
              >
                {level.name}
              </button>
            ))}
          </div>
        </div>
      ),
    },
    {
      id: 'wordlists',
      title: 'Wordlists',
      description: 'Review and test yourself on course wordlists.',
      // Direct navigation on click without an overlay:
      onClick: () => { navigate('/vault/wordlists'); }
    },
    {
      id: 'films',
      title: 'Films & Series',
      description: 'Enhance your listening skills with recommended films and shows.',
      onClick: () => { navigate('/vault/movies'); }
    },
    {
      id: 'comic',
      title: 'Comic Book',
      description: 'Coming soon: a creative comic book project!',
      overlayContent: (
        <div className="overlay-content">
          <h3>Comic Book (Coming Soon)</h3>
          <p>
            Stay tuned for an engaging comic book project designed to help you learn English in a fun and creative way!
          </p>
        </div>
      ),
    },
  ];

  const ErrorBox = ({ message, image }) => {
    return (
      <div className="error-box">
        <img src={image} alt="Error Background" className="error-bg" />
        <div className="error-text">{message}</div>
      </div>
    );
  };

  if (loading) return <p>Loading...</p>;
  if (error) return <p>Error: {error}</p>;
  if (!selectedCourse) {
    return (
      <ErrorBox 
        message="You must choose a course to start!" 
        image={errorImg} 
      />
    );
  }

  // Handler for click on a vault item:
  const handleVaultItemClick = (item) => {
    if (item.onClick) {
      // If the item defines an onClick, execute it directly.
      item.onClick();
    } else {
      // Otherwise, show the overlay with more details.
      setActiveItem(item);
    }
  };

  return (
    <div className="vault-page">
      <h2>Your Vault</h2>
      <div className="vault-grid">
        {vaultItems.map((item) => (
          <div
            key={item.id}
            className="vault-card"
            onClick={() => handleVaultItemClick(item)}
          >
            <h3>{item.title}</h3>
            <p>{item.description}</p>
          </div>
        ))}
      </div>

      {/* Modal Overlay for items with overlayContent */}
      {activeItem && activeItem.overlayContent && (
        <div className="overlay">
          <div className="overlay-inner">
            <button
              className="close-button"
              onClick={() => setActiveItem(null)}
            >
              &times;
            </button>
            {activeItem.overlayContent}
          </div>
        </div>
      )}
    </div>
  );
};

export default VaultPage;
