// RadioGroup.js

import React, { useState } from 'react';
import { domToReact } from 'html-react-parser';

const RadioGroup = ({
  domNode,
  correctAnswer,
  interactionKey,
  interaction,
  updateUserInteraction,
  options,
}) => {
  // Local state: which radio is selected
  const [selectedValue, setSelectedValue] = useState(interaction.value);

  const handleChange = (newValue) => {
    setSelectedValue(newValue);

    // Compare with correctAnswer
    const isCorrect =
      newValue.trim().toLowerCase() === correctAnswer.trim().toLowerCase();

    // Update parent state so it knows the users choice
    updateUserInteraction(interactionKey, {
      value: newValue,
      correct: isCorrect,
    });
  };

  // Start with the <span> class from the HTML
  let className = domNode.attribs.class || '';
  // If you want "var" guaranteed
  if (!className.includes('var')) {
    className = `var ${className}`.trim();
  }

  // Add "correct" or "incorrect" if the user has made a selection
  if (interaction.correct) {
    className += ' correct';
  } else if (interaction.value) {
    className += ' incorrect';
  }

  return (
    <span className={className}>
      {domToReact(domNode.children, {
        replace: (childNode) => {
          // Handle radio <input> ...
          if (
            childNode.type === 'tag' &&
            childNode.name === 'input' &&
            childNode.attribs.type === 'radio'
          ) {
            const radioValue = childNode.attribs.value || '';

            return (
              <input
                key={`${interactionKey}-${radioValue}`}
                type="radio"
                name={interactionKey} // groups them
                value={radioValue}
                checked={selectedValue === radioValue}
                onChange={() => handleChange(radioValue)}
              />
            );
          }

          // If you want to handle <label> explicitly, do something like:
          if (childNode.type === 'tag' && childNode.name === 'label') {
            // Example: parse the label text
            // or attach a "for" attribute to link it to the input (requires an ID).
            // If you just return undefined, it will pass through as normal text.
            return (
              <label>
                {domToReact(childNode.children, options)}
              </label>
            );
          }

          // Otherwise, return undefined so childNode is rendered normally
          return undefined;
        },
      })}
    </span>
  );
};

export default RadioGroup;
