import axios from 'axios';
import { doc, setDoc } from 'firebase/firestore';
import { db } from '../../firebase'; // Adjust the import path as needed

const cleanData = (data) => {
  const cleanedData = {};

  for (const key in data) {
    const value = data[key];

    if (value !== null && value !== undefined) {
      if (typeof value === 'object' && !Array.isArray(value)) {
        // Recursively clean nested objects
        const cleanedNestedData = cleanData(value);

        // Only add the cleaned nested object if it's not empty
        if (Object.keys(cleanedNestedData).length > 0) {
          cleanedData[key] = cleanedNestedData;
        }
      } else if (Array.isArray(value)) {
        // Clean arrays by removing empty values
        const cleanedArray = value.filter(
          (item) => item !== null && item !== undefined && item !== ''
        );

        // Only add the cleaned array if it's not empty
        if (cleanedArray.length > 0) {
          cleanedData[key] = cleanedArray;
        }
      } else if (value !== '') {
        // Add non-empty string or other values
        cleanedData[key] = value;
      }
    }
  }

  return cleanedData;
};

const fetchAndUploadData = async (isAdmin, setLoading, setError) => {
  if (!isAdmin) {
    setError('You do not have permission to perform this action.');
    return;
  }

  setLoading(true);
  setError(null);

  try {
    // Fetch data from Google Sheets
    const response = await axios.get(
      'https://script.google.com/macros/s/AKfycbwmU6aXE2w02a-Pj40FSuqwiOfoYUwuEmiU-2-VgQDXIzg_YkPvukuaLXHinG1_T30l/exec'
    );
    const structuredData = response.data;

    // Ensure all data is wrapped within the "courses" object
    const coursesData = structuredData.courses || structuredData;

    console.log('Fetched Data:', coursesData);

    // Iterate over courses and upload data to Firebase
    for (const courseID in coursesData) {
      if (coursesData.hasOwnProperty(courseID)) {
        let courseData = coursesData[courseID];
        console.log(`Uploading Course: ${courseID}`);

        try {
          // Clean and validate course data
          courseData = cleanData(courseData);

          // Extract modules and remove them from courseData
          const modulesData = courseData.modules || {};
          delete courseData.modules;

          // Ensure the cleaned course data is not empty
          if (Object.keys(courseData).length === 0) {
            throw new Error(
              `Course ${courseID} has no valid data to upload.`
            );
          }

          // Upload course data
          await setDoc(doc(db, 'courses', courseID), courseData);
          console.log(`Course ${courseID} uploaded successfully`);

          // Handle modules within the course
          for (const moduleID in modulesData) {
            if (modulesData.hasOwnProperty(moduleID)) {
              let moduleData = modulesData[moduleID];
              console.log(
                `Uploading Module: ${moduleID} for Course: ${courseID}`
              );

              try {
                // Clean and validate module data
                moduleData = cleanData(moduleData);

                // Extract lessons and workbooks and remove them from moduleData
                const lessonsData = moduleData.lessons || {};
                delete moduleData.lessons;

                const workbooksData = moduleData.workbooks || {};
                delete moduleData.workbooks;

                // Ensure the cleaned module data is not empty
                if (Object.keys(moduleData).length === 0) {
                  throw new Error(
                    `Module ${moduleID} in course ${courseID} has no valid data to upload.`
                  );
                }

                // Upload module data
                await setDoc(
                  doc(db, `courses/${courseID}/modules`, moduleID),
                  moduleData
                );
                console.log(
                  `Module ${moduleID} for Course ${courseID} uploaded successfully`
                );

                // Handle lessons within the module
                for (const lessonID in lessonsData) {
                  if (lessonsData.hasOwnProperty(lessonID)) {
                    let lessonData = lessonsData[lessonID];
                    console.log(
                      `Uploading Lesson: ${lessonID} for Module: ${moduleID}`
                    );

                    try {
                      // Clean and validate lesson data
                      lessonData = cleanData(lessonData);

                      // Ensure the cleaned lesson data is not empty
                      if (Object.keys(lessonData).length === 0) {
                        throw new Error(
                          `Lesson ${lessonID} in module ${moduleID} has no valid data to upload.`
                        );
                      }

                      // Upload lesson data
                      await setDoc(
                        doc(
                          db,
                          `courses/${courseID}/modules/${moduleID}/lessons`,
                          lessonID
                        ),
                        lessonData
                      );
                      console.log(
                        `Lesson ${lessonID} for Module ${moduleID} uploaded successfully`
                      );
                    } catch (err) {
                      console.error(
                        `Failed to upload lesson: ${lessonID} for module: ${moduleID}`,
                        err
                      );
                    }
                  }
                }

                // Handle workbooks within the module
                for (const workbookID in workbooksData) {
                  if (workbooksData.hasOwnProperty(workbookID)) {
                    let workbookData = workbooksData[workbookID];
                    console.log(
                      `Uploading Workbook: ${workbookID} for Module: ${moduleID}`
                    );

                    try {
                      // Clean and validate workbook data
                      workbookData = cleanData(workbookData);

                      // Ensure the cleaned workbook data is not empty
                      if (Object.keys(workbookData).length === 0) {
                        throw new Error(
                          `Workbook ${workbookID} in module ${moduleID} has no valid data to upload.`
                        );
                      }

                      // Upload workbook data
                      await setDoc(
                        doc(
                          db,
                          `courses/${courseID}/modules/${moduleID}/workbooks`,
                          workbookID
                        ),
                        workbookData
                      );
                      console.log(
                        `Workbook ${workbookID} for Module ${moduleID} uploaded successfully`
                      );
                    } catch (err) {
                      console.error(
                        `Failed to upload workbook: ${workbookID} for module: ${moduleID}`,
                        err
                      );
                    }
                  }
                }
              } catch (err) {
                console.error(
                  `Failed to upload module: ${moduleID} for course: ${courseID}`,
                  err
                );
              }
            }
          }
        } catch (err) {
          console.error(`Failed to upload course: ${courseID}`, err);
        }
      }
    }

    setError(null);
  } catch (error) {
    console.error('Error fetching and uploading data:', error);
    setError(`Failed to fetch and upload data: ${error.message}`);
  } finally {
    setLoading(false);
  }
};

export default fetchAndUploadData;
