// ProgressBar.js

import React from 'react';
import '../../styles/CurrentLesson.css';
import '../../styles/LessonPage.css';

const ProgressBar = ({ currentPartIndex, totalParts }) => (
  <div className="progress-bar">
    {[...Array(totalParts).keys()].map((i) => (
      <div
        key={i}
        className={`progress-step ${i === currentPartIndex ? 'active' : ''}`}
      />
    ))}
    <div
      className={`progress-step ${
        currentPartIndex === totalParts ? 'active' : ''
      }`}
    >
      Summary
    </div>
  </div>
);

export default ProgressBar;
