import React, { useState, useEffect } from 'react';
import Reading from './Reading';
import UseOfEng from './UseOfEng';
import Listening from './Listening';
import Embarker from './Embarker';
import ResultPage from './ResultPage';
import { db } from '../../firebase'; // Adjust path accordingly
import { setDoc, doc } from 'firebase/firestore';
import { useAuth } from '../../contexts/AuthContext'; 
import './reading.css';

const EngTest = () => {
  const [currentPart, setCurrentPart] = useState('embarker');
  const [results, setResults] = useState({ reading: null, useOfeng: null, listening: null });
  const [averageLevel, setAverageLevel] = useState(null); // State to hold average level
  const { user } = useAuth(); // Get the current user

  const handleResultsCalculated = (newResults) => {
    setResults((prevResults) => ({
      ...prevResults,
      ...newResults,
    }));
  };

  // Function to convert level to numerical value
  const levelToNumber = (level) => {
    switch (level) {
      case 'A1': return 1;
      case 'A2': return 2;
      case 'B1': return 3;
      case 'B2': return 4;
      case 'C1': return 5;
      case 'C2': return 6;
      default: return 0; // Unknown level
    }
  };

  // Function to convert numerical value back to level
  const numberToLevel = (num) => {
    switch (num) {
      case 1: return 'A1.Elementary';
      case 2: return 'A2.Pre-intermediate';
      case 3: return 'B1.Intermediate';
      case 4: return 'B1+.Advancedintermediate';
      case 5: return 'B2.Upper-intermediate';
      case 6: return 'C1.Advanced';
      default: return 'Unknown';
    }
  };

  // Function to calculate average level
  const calculateAverageLevel = (results) => {
    const levels = Object.values(results)
      .map(result => result?.level ? levelToNumber(result.level) : null)
      .filter(num => num !== null && num > 0); // Filter out null and unknown levels
  
    if (levels.length === 0) return 'Unknown'; // Handle case with no valid levels
  
    const average = levels.reduce((sum, value) => sum + value, 0) / levels.length;
  
    return numberToLevel(Math.round(average)); // Round to nearest level
  };
  

  // Function to save results to Firebase
  const saveResultsToFirebase = async (results, averageLevel) => {
    if (!user) return; // If user is not logged in, exit
  
    try {
      await setDoc(doc(db, 'users', user.uid), {
        listening: results?.listening?.level || 'Unknown',
        reading: results?.reading?.level || 'Unknown',
        useOfEnglish: results?.useOfeng?.level || 'Unknown',
        courseID: averageLevel,
      }, { merge: true }); // Merge to prevent overwriting other user data
      console.log('Results saved successfully');
    } catch (error) {
      console.error('Error saving results: ', error);
    }
  };
  

  // Function to calculate and save results when navigating to ResultPage
  const calculateAndSaveResults = async () => {
    const averageLevel = calculateAverageLevel(results);
    setAverageLevel(averageLevel); // Set average level in state
    console.log('Calculated Results: ', results); // Add this log to check if results are valid
    console.log('Average Level: ', averageLevel);
    await saveResultsToFirebase(results, averageLevel);
  };
  

  const renderPart = () => {
    switch (currentPart) {
      case 'embarker':
        return <Embarker nextPart={() => setCurrentPart('reading')} onResultsCalculated={handleResultsCalculated} />;
      case 'reading':
        return <Reading nextPart={() => setCurrentPart('useofeng')} onResultsCalculated={handleResultsCalculated} />;
      case 'useofeng':
        return <UseOfEng nextPart={() => setCurrentPart('listening')} onResultsCalculated={handleResultsCalculated} />;
      case 'listening':
        return <Listening nextPart={() => { calculateAndSaveResults(); setCurrentPart('testresult'); }} onResultsCalculated={handleResultsCalculated} />;
        case 'testresult':
          if (!results || !averageLevel) {
            return <p>Loading results...</p>; // Add a fallback while calculating results
          }
          return <ResultPage results={results} averageLevel={averageLevel} />;
        
      default:
        return null;
    }
  };

  return (
    <div className="app-container">
      {renderPart()}
    </div>
  );
};

export default EngTest;
