export const runWfpWdgt = async (paymentFields) => {
  const attemptPayment = async () => {
    if (!window.Wayforpay) {
      console.error('WayForPay widget is not available.');
      return;
    }

    const wayforpay = new window.Wayforpay();

    // Prepare payment details for signature generation
    const paymentDetails = {
      merchantAccount: 'anglomova_com',
      merchantDomainName: 'www.anglomova.com',
      orderReference: paymentFields.orderReference,
      orderDate: paymentFields.orderDate,
      amount: paymentFields.amount,
      currency: paymentFields.currency,
      productName: paymentFields.productName,
      productCount: 1,
      productPrice: paymentFields.amount,
      clientFirstName: paymentFields.clientFirstName,
      clientLastName: paymentFields.clientLastName,
      clientEmail: paymentFields.email,
      clientPhone: paymentFields.phone,
      clientAccountId: paymentFields.clientAccountId
    };

    try {
      const signatureResponse = await fetch('https://us-central1-anglomova-429e2.cloudfunctions.net/generateSignature', {
        method: 'POST',
        headers: { 'Content-Type': 'application/json' },
        body: JSON.stringify(paymentDetails),
      });

      if (!signatureResponse.ok) {
        console.error('Failed to fetch the signature.');
        return;
      }

      const { merchantSignature } = await signatureResponse.json();
      if (!merchantSignature) {
        console.error('Failed to generate merchant signature.');
        return;
      }

      const serviceUrl = `https://us-central1-anglomova-429e2.cloudfunctions.net/handleWayForPayCallback?clientAccountId=${paymentDetails.clientAccountId}&email=${encodeURIComponent(paymentDetails.clientEmail)}`;

      // Trigger the WayForPay widget
      wayforpay.run({
        merchantAccount: paymentDetails.merchantAccount,
        merchantDomainName: paymentDetails.merchantDomainName,
        authorizationType: 'SimpleSignature',
        merchantSignature,
        orderReference: paymentDetails.orderReference,
        orderDate: paymentDetails.orderDate,
        amount: paymentDetails.amount,
        currency: paymentDetails.currency,
        productName: paymentDetails.productName,
        productPrice: paymentDetails.productPrice,
        productCount: paymentDetails.productCount,
        clientFirstName: paymentDetails.clientFirstName,
        clientLastName: paymentDetails.clientLastName,
        clientEmail: paymentDetails.clientEmail,
        clientPhone: paymentDetails.clientPhone,
        language: 'AUTO',
        serviceUrl,  // URL for notifications
      });
    } catch (error) {
      console.error('Error while generating signature or starting payment:', error);
    }
  };

  attemptPayment();
};

// Listen for messages from the WayForPay widget
window.addEventListener("message", (event) => {
  switch (event.data) {
    case 'WfpWidgetEventApproved':
      // Handle approved payment
      break;
    case 'WfpWidgetEventDeclined':
      // Handle declined payment
      break;
    case 'WfpWidgetEventPending':
      // Handle pending payment
      break;
    case 'WfpWidgetEventClose':
      // Handle widget closure
      break;
    default:
      console.warn('Unknown event:', event.data);
  }
}, false);
