import React, { useState, useRef, useEffect } from 'react';
import audioFile from '../../assets/listening-test.MP3';

const Listening = ({ nextPart, onResultsCalculated }) => { // Ensure onResultsCalculated is received as a prop
  const [userAnswers, setUserAnswers] = useState({});
  const [playCount, setPlayCount] = useState(0);
  const audioRef = useRef(null);
  const [disableControls, setDisableControls] = useState(false);
  
  const listeningQuestions = [
    {
      id: 1,
      question: 'You hear someone talking on a radio programme. What is the speaker doing?',
      options: [
        'A. recommending that listeners make a certain drink',
        'B. explaining why a drink is becoming more popular',
        'C. telling listeners about a drink they may not know about',
      ],
      correctAnswer: 'C. telling listeners about a drink they may not know about',
    },
    {
      id: 2,
      question: 'You hear someone talking about people who travel a lot when they\'re young. What is his attitude towards these people?',
      options: [
        'A. He is envious of them for having the opportunity.',
        'B. He feels that they are simply wasting their time.',
        'C. He can\'t understand why they do it.',
      ],
      correctAnswer: 'B. He feels that they are simply wasting their time.',
    },
    {
      id: 3,
      question: 'You hear an advertisement for a course. What does the speaker say about the course?',
      options: [
        'A. You need to take a test before being accepted for it.',
        'B. It starts with theory and moves on to practical work.',
        'C. It focuses on your effect on the people you will instruct.',
      ],
      correctAnswer: 'C. It focuses on your effect on the people you will instruct.',
    },
    {
      id: 4,
      question: 'You hear part of a radio interview. Who is being interviewed?',
      options: [
        'A. a film director',
        'B. an actor',
        'C. a screenwriter',
      ],
      correctAnswer: 'B. an actor',
    },
    {
      id: 5,
      question: 'You hear part of a radio report about car drivers. What did the survey discover about a lot of drivers?',
      options: [
        'A. They pay no attention to warning lights.',
        'B. They don\'t know what various symbols in a car relate to.',
        'C. They think there are too many warning lights and symbols in cars.',
      ],
      correctAnswer: 'B. They don\'t know what various symbols in a car relate to.',
    },
    {
      id: 6,
      question: 'You turn on the radio and hear part of a programme.What type of programme is it?',
      options: [
        'A. a review programme',
        'B. a chat show',
        'C. a phone-in',
      ],
      correctAnswer: 'B. a chat show',
    },
    {
      id: 7,
      question: 'You hear part of a radio play. Where is the scene taking place?',
      options: [
          'A. in a restaurant',
          'B. in a car',
          'C. in a house',
      ],
      correctAnswer: 'B. in a car',
    },
    {
      id: 8,
      question: 'You hear a woman talking about running. What aspect of running is she talking about?',
      options: [
        'A. her involvement in running over a period of time',
        'B. why she finds running so enjoyable',
        'C. the importance of running and training with others',
      ],
      correctAnswer: 'A. her involvement in running over a period of time',
    },

  ];

  useEffect(() => {
    // Load answers from local storage when the component mounts
    const storedAnswers = JSON.parse(localStorage.getItem('listeningAnswers')) || {};
    setUserAnswers(storedAnswers);
  }, []);

  const handleAnswer = (questionId, selectedOption) => {
    const updatedAnswers = {
      ...userAnswers,
      [questionId]: selectedOption,
    };

    setUserAnswers(updatedAnswers);
    // Save updated answers to local storage
    localStorage.setItem('listeningAnswers', JSON.stringify(updatedAnswers));
  };

  const renderQuestion = (question) => (
    <li key={question.id} className="question-block">
      <p>{question.question}</p>
      {question.options.map((option, index) => (
        <><button
          key={index}
          style={{
            backgroundColor: userAnswers[question.id] === option ? '#94FFD8' : '#f0f0f0',  // Change color if selected
            color: userAnswers[question.id] === option ? '#000' : '#333',  // Change text color if selected
            cursor: 'pointer',
            border: '1px solid #ccc',
            padding: '10px',
            margin: '5px'
          }}
          onClick={() => handleAnswer(question.id, option)}
          disabled={userAnswers[question.id] !== undefined} // Disable after answering
          className={userAnswers[question.id] === option ? 'selected' : ''}
        >
          {option}
        </button> <br /></>
      ))}
    </li>
  );

  const handlePlay = () => {
    if (playCount < 2) {
      audioRef.current.play(); // Trigger audio play
      setPlayCount(playCount + 1);

      if (playCount + 1 === 2) {
        setDisableControls(true); // Disable controls after max plays
      }
    } else {
      alert('You have reached the maximum number of playbacks.');
    }
  };

  const preventSeek = (e) => {
    if (disableControls) {
      e.preventDefault();
      audioRef.current.currentTime = 0; // Reset to the beginning if dragging is attempted
    }
  };

  const calculateResults = () => {
    const storedAnswers = JSON.parse(localStorage.getItem('listeningAnswers')) || {};
    
    const totalQuestions = listeningQuestions.length; // 8
    const correctAnswers = listeningQuestions.filter(q => storedAnswers[q.id] === q.correctAnswer).length;
  
    const level = getLevel(correctAnswers, totalQuestions);
    
    // Ensure listening results are passed to parent component
    onResultsCalculated({ listening: { totalQuestions, correctAnswers, level } });
    nextPart(); // Move to the next part after calculating results
  };
  

  // New function to determine level
  const getLevel = (correct, total) => {
    const percentage = (correct / total) * 100;
    if (percentage >= 95) return 'C1';
    if (percentage >= 75) return 'B2'; // 6-8 correct
    if (percentage >= 50) return 'B1'; // 4-5 correct
    if (percentage >= 25) return 'A2'; // 2-3 correct
    return 'A1'; // 0-1 correct
  };

  return (
    <>
      <div className="reading-use-of-english-grid">
      <div className="introTest break">
        <h2>Listening</h2>
        <h4>You will hear people talking in 8 different situations. For questions 1-8, choose the best answer A, B, or C</h4>
      </div>
      <div className="introTest break">
        <h5>You may hear the audio only twice without pauses!</h5>
        <audio
          ref={audioRef}
          src={audioFile}
          controls={false} // Disable controls after max plays
          onTimeUpdate={preventSeek} // Prevent seeking
        />
        <button onClick={handlePlay}>Play Audio</button>
        <p>Play count: {playCount} / 2</p>
      </div>
        <ol className="questions overbox">
          <>
          {listeningQuestions.map(renderQuestion)}
          </>
          <div className='break centralized'><button onClick={calculateResults}> NEXT</button></div>
         
        </ol>

      </div>
    </>
  );
};

export default Listening;