import React, { useEffect, useState } from 'react';
import { useAuth } from '../contexts/AuthContext';
import { useLearning } from '../contexts/LearningContext';
import '../styles/Profile.css';
import { useNavigate, Link } from 'react-router-dom';
import { Helmet } from 'react-helmet';
import defUserPic from '../pics/default-user.png';
import step1 from '../pics/starter/prof-test.webp';
import step2 from '../pics/starter/user-course.webp';
import step3 from '../pics/starter/starter-pay.webp';
import nextLS from '../pics/profilePics/LSnext.png';
import nextWB from '../pics/profilePics/WBnext2.png';
import { waitForPendingWrites } from 'firebase/firestore';
import ProfileSidebar from './ProfileSidebar';
import Calendar from '../components/calendar'

const ProfilePage = () => {
  const { user, logout } = useAuth();
  const { selectedCourse, nextLesson, nextWorkbook } = useLearning();
  const { toggleSidebar } = ProfileSidebar;
  const navigate = useNavigate();

  // Handle user logout and navigation
  const handleLogout = async () => {
    try {
      await logout();
      navigate('/');
    } catch (error) {
      console.error('Failed to logout:', error.message);
      alert('Failed to logout: ' + error.message);
    }
  };

  if (!user) {
    return <div className='loader'>Loading...</div>; // Optionally redirect to sign-in page if user is not authenticated
  }

  return (
    <div className="profile-page">
      <ProfileSidebar handleLogout={handleLogout}  onClick={toggleSidebar} />
      <ProfileMainSection user={user} nextLesson={nextLesson} nextWorkbook={nextWorkbook}/>
    </div>
  );
};

const ProfileMainSection = ({ user, nextLesson, nextWorkbook }) => {
  const shouldRenderStartSection = 
    (!user.balance || user.balance === 0) &&
    !user.courseID &&
    (!user.finishedLessons || user.finishedLessons.length === 0);

// Calculate the number of finished lessons and workbooks
const finishedLessonsCount = user?.finishedLessons?.length || 0;
const finishedWorkbooksCount = user?.finishedWorkbooks?.length || 0;

// Calculate total hours spent studying
const totalLessonTime = user?.finishedLessons?.reduce((total, lesson) => total + (lesson.timeSpent || 0), 0) || 0;
const totalWorkbookTime = user?.finishedWorkbooks?.reduce((total, workbook) => total + (workbook.timeSpent || 0), 0) || 0;

// Total time spent in hours
const totalTimeSpent = totalLessonTime + totalWorkbookTime;

  return (  
    <section className="main-section" id="root">

      <ProfileHeader user={user}  totalTimeSpent={totalTimeSpent} />
      {/* Render StartSection based on the conditions */}
      {shouldRenderStartSection ? <StartSection /> : null}
      <BalanceSection user={user}  nextLesson={nextLesson} nextWorkbook={nextWorkbook} />
      <Calendar />
      
      <footer className="development-notice incomplete">in development..nnn.</footer>
    </section>
  );
};

const ProfileHeader = ({ user, totalTimeSpent }) => (
  <div className="profile-header">
    <div className="profile-info">
      <div className="profile-image-container">
        <div className="profile-image-wrapper">
          <img
            loading="lazy"
            src={user.photoURL || defUserPic}
            className="profile-image"
            alt="Profile"
          />
          <div className="profile-name-container">
            <div className="profile-name">{user.displayName || 'No Name'}</div>
          </div>
        </div>
      </div>
      <ProfileDetails user={user} totalHours={totalTimeSpent} />
    </div>
  </div>
);

const ProfileDetails = ({ user, totalHours  }) => (
  <div className="profile-details">
    <ProfileDetail label="ID:" value={user.uid} isID={true} />
    <ProfileDetail label="Native language:" value={user.nativeLanguage || 'Not specified'} />
    <ProfileDetail label="Job/Study:" value={user.job || 'Not specified'} />
    <ProfileDetail label="E-mail:" value={user.email} />
    <ProfileDetail label="Contacts:" value={user.contacts || 'Not specified'} />
    <ProfileDetail label="Goal:" value={user.goal || 'Not specified'} />
    <ProfileDetail label="You've studied:" value={`${totalHours ? `${totalHours} h.` : 'pending'}`} />
  </div>
);

const ProfileDetail = ({ label, value, isID }) => (
  <div className="detail-item">
    <div className="detail-label">{label}</div>
    <div className="detail-value">
      {isID ? (
        <>
          <span className="masked-id">****</span>
          <span className="tooltip">{value}</span>
        </>
      ) : (
        value
      )}
    </div>
  </div>
);



const StartSection = ({ user }) => {
  const steps = [
    { step: "1", title: "Pass a TEST!", description: "Get to know your current level!", imgSrc: step1, to: '/exam', isCompleted: user?.averageLevel },
    { step: "2", title: "Choose course", description: "Select a course that matches your level", imgSrc: step2, isCompleted: user?.course },
    { step: "3", title: "Pay to start", description: "Get the most efficient English lessons after purchase!", imgSrc: step3, isCompleted: false },
  ];

  return (
    <div className="start-section">
      <div className="start-content">
        <div className="start-text">
          <h2 className="start-heading">LET'S<br />START!</h2>
        </div>
        {/* Progress Bar */}
        <div className="progress-container">
          <div className="progress-bar">
            <div className="progress-bar-filled" style={{ width: `${(steps.filter(step => step.isCompleted).length / steps.length) * 100}%` }}></div>
          </div>
          {steps.map((step, index) => (
            <div key={index} className={`progress-step ${step.isCompleted ? 'completed' : index === steps.findIndex(s => !s.isCompleted) ? 'in-progress' : ''}`}>
              Step {step.step}
            </div>
          ))}
        </div>

        {/* Steps Content */}
        <div className="steps-content">
          {steps.map((step, index) => (
            <StepItem
              key={index}
              step={step.step}
              title={step.title}
              description={step.description}
              imgSrc={step.imgSrc}
              to={step.to}
              bgColor={step.isCompleted ? 'green' : ''}
            />
          ))}
        </div>
      </div>
    </div>
  );
};

const StepItem = ({ step, title, description, imgSrc, to, bgColor }) => (
  <Link className="step-item" to={to}>
    <div className="step-box" style={{ backgroundColor: bgColor || '' }}>
      <h2 className="step-title">{title}</h2>
      <p className="step-description">{description}</p>
      <img className="step-img" src={imgSrc} alt={title} />
    </div>
  </Link>
);


const BalanceSection = ({ user, nextLesson, nextWorkbook }) => (
  <div className="balance-section">
    <NextLessonBoxes user={user} nextLesson={nextLesson} nextWorkbook={nextWorkbook}/>
    <div className='nextobox column single'>
    <BalanceBox user={user} to='/pricing' />
    <LessonOptions />
    
    </div>
    
  </div>
);

const BalanceBox = ({ user }) => (
  <div className="balance-box">
    <div className="detail-label">BALANCE:</div>
    <div className="balance-wrapper">
      <div className="balance-title">Lessons: 
        {user ? ` ${user.balance || 'Not available'}` : 'Loading...'} </div>
      <div className="balance-title">Workbooks: 
        {user ? ` ${user.balance || 'Not available'}` : 'Loading...'} </div>
        <Link to="/pricing" className="recharge-btn">
        Recharge
      </Link>
      <div className="recharge-btn incomplete">crypto</div>
    </div>
  </div>
);

const LessonOptions = ({ user }) => {
  // Ensure that the 'user' object exists before trying to access 'user.videocall'
  return (
      <div className="options-list">
        {/* Only show the Videocall button if 'user' and 'user.videocall' exist */}
        {user && user.videocall ? (
          <button className="option-item" onClick={() => window.open(user.videocall)}>
            Videocall
          </button>
        ) : (
          <button className="option-item" onClick={() => alert('No Videocall Link Available')}>
            Videocall
          </button>
        )}

        {/* Other buttons remain visible */}
        <button className="option-item incomplete">pending...</button>
        <button className="option-item incomplete">pending...</button>
      </div>
  );
};


const NextLessonBoxes = ({ user, nextLesson, nextWorkbook }) => {
  const navigate = useNavigate();
  const handleLessonClick = (lesson) => {
    if (user && lesson) {
      navigate(`/course/${user.courseID}/module/${lesson.ModuleID}/lesson/${lesson.id}`);
    }
  };
  const handleWorkbookClick = (workbook) => {
    if (user && workbook) {
      navigate(`/course/${user.courseID}/module/${workbook.ModuleID}/workbook/${workbook.id}`);
    }
  };

  return (
    <div className="itemMain">
      <div className="detail-item wide">
        <div className="detail-label">YOUR COURSE:</div>
        <h3 className='white'>{user.courseID || 'No course selected'}</h3>
      </div>

<div className='nextobox'>
      <div className="next-lesson-box" onClick={() => handleLessonClick(nextLesson)}>
        <div className="next-lesson-text">NEXT LESSON</div>
        {nextLesson ? (
          <div className="lesson-details">
            <h3 className='white'>
            <span className="nextMod" style={{ textTransform: 'uppercase' }}>{nextLesson.ModuleID} </span>
            <span className="nextMod red">{nextLesson.Type} {nextLesson.lessonNum}</span>
            <span className="nextMod aqua">{nextLesson.lessonName}</span>
                </h3>
          </div>
        ) : (
          <p>No upcoming lesson</p>
        )}
        <img 
          className="logoNext" 
          src={nextLS} 
          alt="Next Lesson" 
        />
      </div>

      <div className="next-lesson-box" onClick={() => handleWorkbookClick(nextWorkbook)}>
        <div className="next-lesson-text">NEXT WORKBOOK</div>
        {nextWorkbook ? (
          <div className="workbook-details">
            <h3 className='white'>
            <span className="nextMod" style={{ textTransform: 'uppercase' }}>{nextWorkbook.ModuleID} </span>
            <span className="nextMod aqua">{nextWorkbook.workbookNum}</span>
                </h3>
          </div>
        ) : (
          <p>No upcoming workbook</p>
        )}
        <img 
          className="logoNext" 
          src={nextWB} 
          alt="Next Workbook" 
        />
      </div>
    </div>
    </div>
  );
};

export default ProfilePage;
