import React, { useEffect } from 'react';

const PaymentWidget = () => {
  
  useEffect(() => {
    //    WayForPay   
    const script = document.createElement('script');
    script.src = 'https://secure.wayforpay.com/server/pay-widget.js';
    script.id = 'widget-wfp-script';
    script.async = true;
    document.body.appendChild(script);

    return () => {
      //     
      document.body.removeChild(script);
    };
  }, []);

  const handlePayment = () => {
    if (window.Wayforpay) {
      const wayforpay = new window.Wayforpay();

      wayforpay.run(
        {
          merchantAccount: 'test_merch_n1',
          merchantDomainName: 'www.market.ua',
          authorizationType: 'SimpleSignature',
          merchantSignature: 'flk3409refn54t54t*FNJRET',
          orderReference: 'DH783023',
          orderDate: Math.floor(new Date().getTime() / 1000).toString(), // Unix timestamp
          amount: '1547.36',
          currency: 'UAH',
          productName: [' Intel Core i5-4670 3.4GHz'],
          productPrice: ['1000'],
          productCount: ['1'],
          clientFirstName: '',
          clientLastName: '',
          clientEmail: 'some@mail.com',
          clientPhone: '380631234567',
          language: 'UA',
        },
        function (response) {
          //   
          console.log('Transaction approved', response);
        },
        function (response) {
          //   
          console.log('Transaction declined', response);
        },
        function (response) {
          //     
          console.log('Transaction pending or in process', response);
        }
      );
    } else {
      console.error('WayForPay script not loaded');
    }
  };

  return (
    <div>
      <h1>  WayForPay</h1>
      <button onClick={handlePayment}></button>
    </div>
  );
};

export default PaymentWidget;
