// src/AppRouter.js

import React from 'react';
import { Routes, Route } from 'react-router-dom';
import ProfilePage from './tabs/Profile';
import Courses from './tabs/Courses';
import Main from './tabs/Main';
import CurrentLesson from './tabs/CurrentLesson';
import LessonPage from './tabs/lesson/LessonPage';
import WorkbookPage from './tabs/lesson/WorkbookPage';
import PricingPage from './tabs/Pricing';
import VaultPage from './tabs/Vault';
import AdminPage from './tabs/AdminPage';
import ExamPage from './tabs/exam/EngTest';
import WFP from './tabs/testinWFP';
import WordlistsOverview from './tabs/vault/WordlistsOverview';
import WordlistDetail from './tabs/vault/WordlistDetail';
import Movies from './tabs/vault/Movies';

// Import Irregular Verbs Trainer and verb data
import IrregularVerbsTrainer from './tabs/vault/irregularVerbsTrainer';
import a0Verbs from './tabs/vault/a0Verbs';
import a1Verbs from './tabs/vault/a1Verbs';
import a2Verbs from './tabs/vault/a2Verbs';
import b1Verbs from './tabs/vault/b1Verbs';
import advancedVerbs from './tabs/vault/advancedVerbs';


const AppRouter = () => {
  return (
    <Routes>
      <Route path="/" element={<Main />} />
      <Route path="/profile" element={<ProfilePage />} />
      <Route path="/courses" element={<Courses />} />
      <Route path="/lesson" element={<CurrentLesson />} />
      <Route path="/course/:courseID/module/:moduleID/lesson/:lessonID" element={<LessonPage />} />
      <Route path="/course/:courseID/module/:moduleID/workbook/:workbookID" element={<WorkbookPage />} />
      <Route path="/pricing" element={<PricingPage />} />
      <Route path="/vault" element={<VaultPage />} />
      {/* Add routes for each Irregular Verbs Trainer level */}
      <Route path="/vault/a0" element={<IrregularVerbsTrainer level="A0" verbs={a0Verbs} />} />
      <Route path="/vault/a1" element={<IrregularVerbsTrainer level="A1" verbs={a1Verbs} />} />
      <Route path="/vault/a2" element={<IrregularVerbsTrainer level="A2" verbs={a2Verbs} />} />
      <Route path="/vault/b1" element={<IrregularVerbsTrainer level="B1" verbs={b1Verbs} />} />
      <Route path="/vault/advanced" element={<IrregularVerbsTrainer level="Advanced" verbs={advancedVerbs} />} />
      <Route path="/vault/wordlists" element={<WordlistsOverview />} />
      <Route path="/vault/wordlists/:moduleId/:lessonId" element={<WordlistDetail />} />
      <Route path="/vault/movies" element={<Movies />} />
      <Route path="/admin" element={<AdminPage />} />
      <Route path="/exam" element={<ExamPage />} />
      <Route path="/wfp" element={<WFP />} />
    </Routes>
  );
};

export default AppRouter;
