// LessonSummary.js

import React from 'react';
import '../../styles/CurrentLesson.css';
import '../../styles/LessonPage.css';

const WorkbookSummary = ({
  startTime,
  correctAnswers,
  totalQuestions,
  handleFinishWorkbook,
}) => {
  const timeSpent = Math.round((Date.now() - startTime) / 1000);

  return (
    <div className="endpoint">
      <h2>Workbook Summary</h2>
      <p>Time Spent: {timeSpent} seconds</p>
      <p>
        Correct Answers: {correctAnswers} / {totalQuestions}
      </p>
      <button onClick={handleFinishWorkbook}>Finish Workbook</button>
    </div>
  );
};

export default WorkbookSummary;
