// src/components/WordlistsOverview.js
import React, { useEffect, useState } from 'react';
import { useLearning } from '../../contexts/LearningContext';
import { useAuth } from '../../contexts/AuthContext';
import { useNavigate } from 'react-router-dom';
import CardBg from '../../assets/WORDLISTBG.webp';

function WordlistsOverview() {
  const { selectedCourse, loading, error, fetchTestResults } = useLearning();
  const { user } = useAuth();
  const [testResults, setTestResults] = useState({});
  const navigate = useNavigate();

  useEffect(() => {
    const fetchAllResults = async () => {
      if (!user || !selectedCourse) return;

      const results = {};

      for (const module of selectedCourse.modules || []) {
        if (!module.lessons) continue;

        for (const lesson of module.lessons || []) {
          if (!lesson || !lesson.WORDLIST) continue;

          try {
            const lessonID = `${module.id}/${lesson.id}`;
            const result = await fetchTestResults(module.id, lesson.id);

            if (result && result.userInteractions) {
              results[lessonID] = {
                score: result.userInteractions.score || 0,
                performance: result.userInteractions.performance || 'Not Started',
                date: result.userInteractions.date
                  ? new Date(result.userInteractions.date)
                  : null,
              };
            } else {
              results[lessonID] = {
                score: 0,
                performance: 'Not Started',
                date: null,
              };
            }
          } catch (err) {
            console.error(`Error fetching results for lesson ${lesson?.id}:`, err);
            results[`${module.id}/${lesson?.id || 'unknown'}`] = {
              score: 0,
              performance: 'Error Fetching Data',
              date: null,
            };
          }
        }
      }

      setTestResults(results);
    };

    fetchAllResults();
  }, [selectedCourse, user, fetchTestResults]);

  if (loading) return <p>Loading wordlists...</p>;
  if (error) return <p>Error: {error}</p>;
  if (!selectedCourse) return <p>No course selected.</p>;

  const getProgressColor = (score) => {
    if (score >= 75) return 'green';
    if (score >= 50) return 'orange';
    return 'red';
  };

  return (
    <div style={{ margin: '2rem' }}>
      <h1>Wordlists Overview</h1>
      {selectedCourse.modules?.map((module) => (
        <div key={module.id} className="module-card" style={styles.moduleCard}>
          <h2 class="wb-tag">Module: {module.name || module.id}</h2>
          <div className="card-bundle" style={styles.cardBundle}>
            {module.lessons?.map((lesson) => {
              if (!lesson.WORDLIST) return null;

              const lessonID = `${module.id}/${lesson.id}`;
              const lessonResult = testResults[lessonID] || {
                score: 0,
                performance: 'Not Started',
                date: null,
              };

              return (
                <div key={lesson.id} className="lesson-item" style={styles.lessonItem}>
                  <h3>{lesson.Type} {lesson.lessonNum}: <br />{lesson.lessonName}</h3>
                  <div>
          <div className="progress-bar-container">
            <div
              className="wb-progress-bar"
              style={{
                width: `${lessonResult.score}%`,
                backgroundColor: getProgressColor(lessonResult.score),
              }}
            >
            </div>
          </div>
                  </div>
                  <button
                    onClick={() => navigate(`/vault/wordlists/${module.id}/${lesson.id}`)}
                    style={styles.button}
                  >
                    View Wordlist
                  </button>
                </div>
              );
            })}
          </div>
        </div>
      ))}
    </div>
  );
}


// Optional: Define some basic styles
const styles = {
  moduleCard: {
    border: '1px solid #ccc',
    padding: '1rem',
    marginBottom: '2rem',
    borderRadius: '8px',
    backgroundImage: `url(${CardBg})`,
    backgroundSize: 'cover',
    backgroundPosition: 'center',
    backgroundRepeat: 'no-repeat',
  },
  cardBundle: {
    display: 'flex',
    flexWrap: 'wrap',
    gap: '1rem',
  },
  lessonItem: {
    border: '1px solid #ddd',
    padding: '1rem',
    borderRadius: '6px',
    width: '250px',
    backgroundColor: '#fff',
    boxShadow: '2px 2px 5px rgba(0,0,0,0.1)',
  },
  button: {
    marginTop: '0.5rem',
    padding: '0.5rem 1rem',
    border: 'none',
    backgroundColor: '#007bff',
    color: '#fff',
    borderRadius: '4px',
    cursor: 'pointer',
  },
  
};

export default WordlistsOverview;
