import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { useLearning } from '../contexts/LearningContext';
import { useAuth } from '../contexts/AuthContext';
import '../styles/CurrentLesson.css';
import '../styles/Errorbox.css';
import error from '../pics/error.webp';

// Import graphic icons for the access levels
import unlockedIcon from '../assets/lessonTool/finished.png';
import nextIcon from '../assets/lessonTool/nextitem.png';
import blockedIcon from '../assets/lessonTool/blocked.png';

import pageBg from '../pics/currentlessonbg.webp';

/* -------------------- Next Item Card -------------------- */
const NextItemCard = ({ item, type, onClick }) => {
  // Get the user's finished lessons/workbooks from authContext
  const { user } = useAuth();
  const finishedLessons = user.finishedLessons;
  const finishedWorkbooks = user.finishedWorkbooks;

  // Get the next lesson/workbook from the learning context
  const { nextLesson, nextWorkbook } = useLearning();

  let status;
  if (type === 'lesson') {
    // Compare the lesson's id with the finishedLessons objects lessonID.
    if (finishedLessons && finishedLessons.some(f => f.lessonID === item.id)) status = 'unlocked';
    else if (nextLesson && nextLesson.id === item.id) status = 'next';
    else status = 'blocked';
  } else {
    if (finishedWorkbooks && finishedWorkbooks.some(w => w.workbookID === item.id)) status = 'unlocked';
    else if (nextWorkbook && nextWorkbook.id === item.id) status = 'next';
    else status = 'blocked';
  }

  console.log("User's Finished Lessons:", finishedLessons);
  console.log("Current Item:", item);

  return (
    <div
      className="card"
      onClick={() => (status === 'next' || status === 'unlocked') && onClick(item)}
      style={{ backgroundImage: `url(${item.BGimg || ''})` }}
    >
      <div className="card-image-container">
        <div className="dark-overlay"></div>
        <div className="access-overlay">
          {status === 'unlocked' && (
            <img src={unlockedIcon} alt="Unlocked" className="access-icon" />
          )}
          {status === 'next' && (
            <img src={nextIcon} alt="Next" className="access-icon" />
          )}
          {status === 'blocked' && (
            <img src={blockedIcon} alt="Blocked" className="access-icon" />
          )}
        </div>
      </div>
      <div className="card-content">
        {type === 'lesson' ? (
          <>
            <div className="card-content-top cardbright">
              NEXT {item.Type}
            </div>
            <h3 className="white">
              Lesson {item.lessonNum}:<br />
              {item.lessonName}
            </h3>
          </>
        ) : (
          <>
            <div className="card-content-top carddark">
              NEXT {item.Type}
            </div>
            <h3 className="white">
              Workbook {item.workbookNum}
            </h3>
          </>
        )}
      </div>
    </div>
  );
};

/* -------------------- Modules List -------------------- */
const ModulesList = ({ modules, onSelectModule }) => {
  return (
    <div className="modules-list">
      {modules.map((module) => (
        <div
          key={module.id}
          className="card-item"
          onClick={() => onSelectModule(module)}
        >
          {module.modImg ? (
            <img className="listImg" src={module.modImg} alt={module.modName} />
          ) : (
            <div className="listImg-placeholder">No Image</div>
          )}
          <div className="card-data">
            <div className="module-num top">{module.modNum}</div>
            <div>{module.modName}</div>
          </div>
        </div>
      ))}
    </div>
  );
};

/* -------------------- Module Details Overlay -------------------- */
const ModuleDetailsOverlay = ({
  module,
  lessons,
  workbooks,
  onClose,
  onLessonClick,
  onWorkbookClick,
}) => {
  // Get the finished lessons/workbooks from the user's profile
  const { user } = useAuth();
  const finishedLessons = user.finishedLessons;
  const finishedWorkbooks = user.finishedWorkbooks;

  // Get next items from the learning context
  const { nextLesson, nextWorkbook } = useLearning();

  // For each lesson in the overlay, check finishedLessons using the Firebase property "lessonID".
  const getLessonStatus = (lesson) => {
    if (finishedLessons && finishedLessons.some(f => f.lessonID === lesson.id)) return 'unlocked';
    else if (nextLesson && nextLesson.id === lesson.id) return 'next';
    else return 'blocked';
  };

  // Similarly, check finishedWorkbooks using "workbookID"
  const getWorkbookStatus = (workbook) => {
    if (finishedWorkbooks && finishedWorkbooks.some(w => w.workbookID === workbook.id)) return 'unlocked';
    else if (nextWorkbook && nextWorkbook.id === workbook.id) return 'next';
    else return 'blocked';
  };

  if (!module) return null;

  return (
    <div className="mod">
      <div className="mod-det" onClick={onClose}>
        <div className="mod-det-cont" onClick={(e) => e.stopPropagation()}>
          <div className="mod-det-head">
            <div className="mod-det-head-item">
              <h3 className="black">
                {module.modNum}: {module.modName}
              </h3>
              <div className="close-btn" onClick={onClose}></div>
            </div>
          </div>

          {/* Lessons List */}
          <div>
            <h2>LESSONS</h2>
            <div className="lessons-list">
              {lessons.length > 0 ? (
                lessons.map((lesson) => {
                  const status = getLessonStatus(lesson);
                  return (
                    <div
                      key={lesson.id}
                      className={`card-item ${status}`}
                      onClick={() =>
                        (status === 'next' || status === 'unlocked') && onLessonClick(lesson)
                      }
                    >
                      <div className="image-container">
                        {lesson.BGimg ? (
                          <img className="listImg" src={lesson.BGimg} alt={lesson.lessonName} />
                        ) : (
                          <div className="listImg-placeholder">No Image</div>
                        )}
                        <div className="dark-overlay"></div>
                        <div className="access-overlay">
                          {status === 'unlocked' && (
                            <img src={unlockedIcon} alt="Unlocked" className="access-icon" />
                          )}
                          {status === 'next' && (
                            <img src={nextIcon} alt="Next" className="access-icon" />
                          )}
                          {status === 'blocked' && (
                            <img src={blockedIcon} alt="Blocked" className="access-icon" />
                          )}
                        </div>
                      </div>
                      <div className="card-text">
                        {lesson.lessonNum}: {lesson.lessonName}
                      </div>
                    </div>
                  );
                })
              ) : (
                <div>No lessons available in this module.</div>
              )}
            </div>
          </div>
          <div>
            {/* Workbooks List */}
            <h2>WORKBOOKS</h2>
            <div className="workbooks-list">
              {workbooks.length > 0 ? (
                workbooks.map((workbook) => {
                  const status = getWorkbookStatus(workbook);
                  return (
                    <div
                      key={workbook.id}
                      className={`card-item workbook-item ${status}`}
                      onClick={() =>
                        (status === 'next' || status === 'unlocked') && onWorkbookClick(workbook)
                      }
                    >
                      <div className="image-container">
                        {workbook.BGimg ? (
                          <img className="listImg" src={workbook.BGimg} alt={workbook.workbookName} />
                        ) : (
                          <div className="listImg-placeholder">No Image</div>
                        )}
                        <div className="dark-overlay"></div>
                        <div className="access-overlay">
                          {status === 'unlocked' && (
                            <img src={unlockedIcon} alt="Unlocked" className="access-icon" />
                          )}
                          {status === 'next' && (
                            <img src={nextIcon} alt="Next" className="access-icon" />
                          )}
                          {status === 'blocked' && (
                            <img src={blockedIcon} alt="Blocked" className="access-icon" />
                          )}
                        </div>
                      </div>
                      <div className="card-text">
                        {workbook.workbookNum}
                      </div>
                    </div>
                  );
                })
              ) : (
                <div>No workbooks available in this module.</div>
              )}
            </div>
          </div>
          {/* Module Descriptions */}
          <div className="mod-desk-bot">
            <div
              className="mod-det-desc long"
              dangerouslySetInnerHTML={{ __html: module.modFullDescription || '' }}
            />
            <div
              className="mod-det-item short"
              dangerouslySetInnerHTML={{ __html: module.modDescription || '' }}
            />
          </div>
        </div>
      </div>
    </div>
  );
};

/* -------------------- Main CurrentLesson Component -------------------- */
const CurrentLesson = () => {
  const { user } = useAuth();
  const { selectedCourse, nextLesson, nextWorkbook } = useLearning();
  const navigate = useNavigate();

  const [selectedModule, setSelectedModule] = useState(null);
  const [moduleLessons, setModuleLessons] = useState([]);
  const [moduleWorkbooks, setModuleWorkbooks] = useState([]);

  // Update lessons and workbooks when a module is selected.
  useEffect(() => {
    if (selectedModule && selectedCourse?.modules) {
      const currentModule = selectedCourse.modules.find(
        (mod) => mod.id === selectedModule.id
      );
      if (currentModule) {
        setModuleLessons(currentModule.lessons || []);
        setModuleWorkbooks(currentModule.workbooks || []);
      }
    } else {
      setModuleLessons([]);
      setModuleWorkbooks([]);
    }
  }, [selectedModule, selectedCourse]);

  // Navigate to the selected lesson.
  const handleLessonClick = (lesson) => {
    if (user && lesson) {
      navigate(`/course/${user.courseID}/module/${lesson.ModuleID}/lesson/${lesson.id}`);
    }
  };

  // Navigate to the selected workbook.
  const handleWorkbookClick = (workbook) => {
    if (user && workbook) {
      navigate(`/course/${user.courseID}/module/${workbook.ModuleID}/workbook/${workbook.id}`);
    }
  };

  // When a module is clicked from the modules list.
  const handleModuleClick = (module) => {
    setSelectedModule(module);
  };

  const closeOverlay = () => {
    setSelectedModule(null);
  };

  // Error component for missing user/course data.
  const ErrorBox = ({ message, image }) => {
    return (
      <div className="error-box">
        <img src={image} alt="Error Background" className="error-bg" />
        <div className="error-text">{message}</div>
      </div>
    );
  };

  if (!user) {
    return <ErrorBox message="Register or Sign in to start" image={error} />;
  }
  if (!selectedCourse) {
    return <ErrorBox message="You must choose a course to start!" image={error} />;
  }
  if (!selectedCourse.modules || selectedCourse.modules.length === 0) {
    return <ErrorBox message="No modules available for this course." image={error} />;
  }

  return (
    <div className="lesson-overview" style={{ backgroundImage: `url(${pageBg})` }}>
      <div className="wrapper">
        <div>
          {/* Next Lesson Card */}
          <div className="wrapper-component">
            <NextItemCard
              item={nextLesson}
              type="lesson"
              onClick={handleLessonClick}
            />
          </div>

          {/* Next Workbook Card */}
          <div className="wrapper-component">
            <NextItemCard
              item={nextWorkbook}
              type="workbook"
              onClick={handleWorkbookClick}
            />
          </div>
        </div>

        {/* Modules List */}
        <div className="wrapper-component righty">
          <h2 className="right-cont">Content of {user.courseID}</h2>
          <div className="right-info">
            <ModulesList modules={selectedCourse.modules} onSelectModule={handleModuleClick} />
          </div>
        </div>

        {/* Module Details Overlay */}
        <ModuleDetailsOverlay
          module={selectedModule}
          lessons={moduleLessons}
          workbooks={moduleWorkbooks}
          onClose={closeOverlay}
          onLessonClick={handleLessonClick}
          onWorkbookClick={handleWorkbookClick}
        />
      </div>
    </div>
  );
};

export default CurrentLesson;
