import React, { useState, useEffect } from 'react';
import { collection, getDocs, doc, updateDoc } from 'firebase/firestore';
import { db } from '../../firebase'; // Adjust the import path as needed
import '../../styles/AdminPage.css';

const UserManagement = () => {
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [editingUser, setEditingUser] = useState(null);
  const [formData, setFormData] = useState({});
  const [visibleSections, setVisibleSections] = useState({});

  useEffect(() => {
    const fetchUsers = async () => {
      try {
        const querySnapshot = await getDocs(collection(db, 'users'));
        const usersList = [];
        querySnapshot.forEach((doc) => {
          usersList.push({ id: doc.id, ...doc.data() });
        });
        setUsers(usersList);
      } catch (err) {
        console.error('Error fetching users:', err);
        setError('Failed to fetch users.');
      } finally {
        setLoading(false);
      }
    };

    fetchUsers();
  }, []);

  const handleEditClick = (user) => {
    setEditingUser(user.id);
    setFormData(user);
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData((prevData) => ({
      ...prevData,
      [name]: value,
    }));
  };

  const handleSaveClick = async () => {
    if (editingUser) {
      try {
        await updateDoc(doc(db, 'users', editingUser), formData);
        setUsers((prevUsers) =>
          prevUsers.map((user) => (user.id === editingUser ? formData : user))
        );
        setEditingUser(null);
      } catch (err) {
        console.error('Error updating user:', err);
        setError('Failed to update user.');
      }
    }
  };

  const renderArray = (arr) => (
    <ul>
      {arr.map((item, index) => (
        <li key={index}>{JSON.stringify(item)}</li>
      ))}
    </ul>
  );

  if (loading) return <p>Loading users...</p>;
  if (error) return <p style={{ color: 'red' }}>{error}</p>;

  return (
    <div className="admin-container">
      {/* Sidebar */}
      <div className="sidebar-adm">
        <h2>Admin Panel</h2>
        <ul>
          <li className="active">User Management</li>
          <li><a href="#reports">Reports</a></li>
          <li><a href="#settings">Settings</a></li>
        </ul>
        <button>Generate Report</button>
      </div>

      {/* Main Content */}
      <div className="main-content">
        <div className="user-stats">
          <div className="stats-card">
            <h3>Total Users</h3>
            <p>{users.length}</p>
          </div>
          <div className="stats-card">
            <h3>Active Users</h3>
            <p>{users.filter((user) => user.isActive).length}</p>
          </div>
        </div>

        {/* User Table */}
        <div className="user-table">
          <h3>User List</h3>
          <table>
            <thead>
              <tr>
                <th>Name</th>
                <th>Email</th>
                <th>Course</th>
                <th>Actions</th>
              </tr>
            </thead>
            <tbody>
              {users.map((user) => (
                <tr key={user.id}>
                  {editingUser === user.id ? (
                    <>
                      <td>
                        <input
                          type="text"
                          name="name"
                          value={formData.name || ''}
                          onChange={handleInputChange}
                        />
                      </td>
                      <td>
                        <input
                          type="email"
                          name="email"
                          value={formData.email || ''}
                          onChange={handleInputChange}
                        />
                      </td>
                      <td>
                        <input
                          type="text"
                          name="courseID"
                          value={formData.courseID || ''}
                          onChange={handleInputChange}
                        />
                      </td>
                      <td>
                        <button onClick={handleSaveClick}>Save</button>
                        <button onClick={() => setEditingUser(null)}>Cancel</button>
                      </td>
                    </>
                  ) : (
                    <>
                      <td>{user.name}</td>
                      <td>{user.email}</td>
                      <td>{user.courseID}</td>
                      <td>
                        <button onClick={() => handleEditClick(user)}>Edit</button>
                      </td>
                    </>
                  )}
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
};

export default UserManagement;
