// src/components/Vault/IrregularVerbsTrainer.js

import React, { useState } from 'react';
import '../../styles/IrregularVerbsTrainer.css'; // We'll create this CSS file next

const IrregularVerbsTrainer = ({ level, verbs }) => {
  const [userAnswers, setUserAnswers] = useState({});
  const [isBlurred, setIsBlurred] = useState(true);

  const handleInputChange = (verbId, formType, value) => {
    setUserAnswers((prev) => ({
      ...prev,
      [verbId]: {
        ...prev[verbId],
        [formType]: value,
      },
    }));
  };

  const checkAnswer = (userInput, correctAnswers) => {
    if (!userInput) return false;
  
    // Ensure correctAnswers is always a string or an array of strings
    let answersArray = [];
  
    if (Array.isArray(correctAnswers)) {
      // If correctAnswers is already an array, use it directly
      answersArray = correctAnswers.map((ans) => ans.trim().toLowerCase());
    } else if (typeof correctAnswers === 'string') {
      // If correctAnswers is a string, split it into an array by '/'
      answersArray = correctAnswers.split('/').map((ans) => ans.trim().toLowerCase());
    } else {
      // If correctAnswers is neither a string nor an array, return false
      return false;
    }
  
    // Check if the user's input matches any of the correct answers
    return answersArray.includes(userInput.trim().toLowerCase());
  };
  

  return (
    <div className="trainer-container">
      <div className='solid'>
      <h1 style={{color: 'black!important'}}>Irregular Verbs Trainer - Level {level}</h1>
      <h3>LVL: {level}</h3>
      <div className="block">
         We call <b>Regular verbs</b> the ones that take <b>-ed</b> ending for Past and Perfect Tenses (<i>e.g. show - showed, call - called</i>). <br />
         We call <b>Irregular verbs</b> the ones that <b>do not</b> take <b>-ed</b> ending and may change completely.<br />
         Instruction:
        <ul>
          <li> Hover over the left part. Examine irregular verbs</li>
          <li> Complete the table to the right.</li>
          <li> You may peep. Try not just to rewrite the correct answer, but to memorize it.</li>
        </ul>
      </div>
      </div>
      <div
        className={`left ${isBlurred ? 'blur' : 'unblur'}`}
        onMouseEnter={() => setIsBlurred(false)}
        onMouseLeave={() => setIsBlurred(true)}
      >
        <table className="verb-table">
          <thead>
            <tr>
              <th>Base Form (Infinitive)</th>
              <th>Past Simple</th>
              <th>Past Participle</th>
            </tr>
          </thead>
          <tbody>
            {verbs.map((verb) => (
              <tr key={verb.id}>
                <td>{verb.base}</td>
                <td>{verb.past}</td>
                <td>{verb.pastParticiple}</td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
      <div className="right">
        <table className="check-table">
          <thead>
            <tr>
              <th>Base Form</th>
              <th>Past Simple</th>
              <th>Past Participle</th>
            </tr>
          </thead>
          <tbody>
            {verbs.map((verb) => (
              <tr key={verb.id}>
                <td>
                  <label htmlFor={`${verb.base}_past`}>{verb.base}</label>
                </td>
                <td>
                  <input
                    type="text"
                    className='trainer'
                    id={`${verb.base}_past`}
                    value={userAnswers[verb.id]?.past || ''}
                    onChange={(e) => handleInputChange(verb.id, 'past', e.target.value)}
                  />
                  {checkAnswer(userAnswers[verb.id]?.past, verb.past) && (
                    <span className="correct-indicator"></span>
                  )}
                </td>
                <td>
                  <input
                    type="text"
                    className='trainer'
                    id={`${verb.base}_participle`}
                    value={userAnswers[verb.id]?.participle || ''}
                    onChange={(e) => handleInputChange(verb.id, 'participle', e.target.value)}
                  />
                  {checkAnswer(userAnswers[verb.id]?.participle, verb.pastParticiple) && (
                    <span className="correct-indicator"></span>
                  )}
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default IrregularVerbsTrainer;
