// src/components/CoursesPage/CoursesPage.jsx

import React, { useState } from 'react';
import { coursesData } from '../tabs/courses/AllCourses';
import CourseCard from '../tabs/courses/CourseCard';
import CourseOverlay from '../tabs/courses/CourseOverlay';
import '../styles/Courses.css';

const CoursesPage = () => {
  const [selectedCourse, setSelectedCourse] = useState(null);

  const openOverlay = (course) => {
    setSelectedCourse(course);
  };

  const closeOverlay = () => {
    setSelectedCourse(null);
  };

  return (
    <div className="courses-page">
      {/* Optional Hero Section */}
      <section className="courses-hero">
        <h1>Explore Our English Courses</h1>
        <p>Select a level and start your journey!</p>
      </section>

      {/* Grid of Cards */}
      <div className="courses-grid">
        {coursesData.map((course) => (
          <CourseCard 
            key={course.id} 
            course={course} 
            onClick={() => openOverlay(course)}
          />
        ))}
      </div>

      {/* Overlay / Modal for Details */}
      {selectedCourse && (
        <CourseOverlay 
          course={selectedCourse}
          onClose={closeOverlay}
        />
      )}
    </div>
  );
};

export default CoursesPage;
