import React, { useState } from 'react';
import { useParams, useNavigate } from 'react-router-dom';
import { useLearning } from '../../contexts/LearningContext';
import { useAuth } from '../../contexts/AuthContext';
import WordlistTest from './WordListTest';
// Optional: for celebration when score is 100%
import Confetti from 'react-confetti';

function WordlistDetail() {
  const { moduleId, lessonId } = useParams();
  const { selectedCourse, finishLesson } = useLearning();
  const { user } = useAuth();
  const navigate = useNavigate();

  // Local state for whether we are in test mode, the test score, and confetti
  const [testActive, setTestActive] = useState(false);
  const [testScore, setTestScore] = useState(null);
  const [showConfetti, setShowConfetti] = useState(false);

  // New state for paging (grouping) the wordlist
  const pageSize = 20; // Adjust this value for fewer or more words per page
  const [currentPage, setCurrentPage] = useState(0);

  // Find the correct module and lesson
  const moduleData = selectedCourse?.modules?.find((m) => m.id === moduleId);
  const lesson = moduleData?.lessons?.find((l) => l.id === lessonId);

  if (!lesson || !lesson.WORDLIST) {
    return (
      <div>
        <p>No wordlist found for lesson <em>{lessonId}</em>.</p>
        <button onClick={() => navigate('/vault/wordlists')}>Back to Wordlists</button>
      </div>
    );
  }

  // Parse the wordlist data if it's a JSON string
  let wordlistArr = [];
  if (typeof lesson.WORDLIST === 'string') {
    try {
      wordlistArr = JSON.parse(lesson.WORDLIST);
    } catch (err) {
      console.error('Error parsing WORDLIST JSON:', err);
      return <p>Could not parse wordlist data.</p>;
    }
  } else if (Array.isArray(lesson.WORDLIST)) {
    wordlistArr = lesson.WORDLIST;
  }

  // Compute total pages and current page words
  const totalPages = Math.ceil(wordlistArr.length / pageSize);
  const displayedWords = wordlistArr.slice(currentPage * pageSize, (currentPage + 1) * pageSize);

  // Called when test is completed.
  // If you want to test only the current group, you can pass "displayedWords" instead of wordlistArr.
  const handleTestComplete = (score) => {
    setTestScore(score);
    setTestActive(false);

    if (score === 100) {
      finishLesson(`${moduleId}/${lessonId}`, {
        wordlistScore: 100,
        wordlistCompleted: true,
      });
      setShowConfetti(true);
      setTimeout(() => setShowConfetti(false), 5000);
    }
  };

  if (testActive) {
    return (
      <div>
        {/* 
          You have two choices here: test the whole list or just the current group.
          To test only the current page, pass displayedWords instead:
          wordlist={displayedWords}
        */}
        <WordlistTest
          wordlist={displayedWords}
          userLang={user?.nativeLanguage || 'en'}
          onTestComplete={handleTestComplete}
          moduleID={moduleId}
          lessonID={lessonId}
        />
      </div>
    );
  }

  return (
    <div>
      <div class="headering-wl">
        <h1>Wordlist for Lesson: {lesson.title || lesson.id}</h1>
      <div className="action-buttons" style={{ marginTop: '1rem' }}>
        <button onClick={() => setTestActive(true)}>
          {testScore === 100 ? 'Retake Test for Fun' : 'Take Wordlist Test'}
        </button>
        <button style={{ marginLeft: '1rem' }} onClick={() => navigate('/vault/wordlists')}>
          Back to Wordlists
        </button>
      </div>
      </div>
      {showConfetti && <Confetti width={window.innerWidth} height={window.innerHeight} />}

      <ul className="wordlist">
        {displayedWords.map((item, i) => (
          <li key={i} className="wordlist-item">
            <div className="word-header">
              <div className="word">
                {item.word}
                {item.partOfSpeech && (
                  <em className="part-of-speech"> ({item.partOfSpeech})</em>
                )}
              </div>
              {item.translations && (
                <span className="translation">
                  {item.translations[user?.nativeLanguage] || 'N/A'}
                </span>
              )}
            </div>
            <div className="word-details">
              {Object.entries({
                Definition: item.elementaryDefinition || 'N/A',
                Example: item.example || 'N/A',
              }).map(([label, value]) => (
                <div key={label} className="word-info">
                  <strong>{label}:</strong>{' '}
                  {typeof value === 'object' && value !== null ? (
                    <ul>
                      {Object.entries(value).map(([key, val]) => (
                        <li key={key}>
                          <strong>{key}:</strong> {val}
                        </li>
                      ))}
                    </ul>
                  ) : (
                    value
                  )}
                </div>
              ))}
            </div>
          </li>
        ))}
      </ul>

      {/* Page Navigation */}
      <div className="pagination-controls" style={{ margin: '1rem 0' }}>
        {currentPage > 0 && (
          <button onClick={() => setCurrentPage(currentPage - 1)}>
            Previous
          </button>
        )}
        <span style={{ margin: '0 1rem' }}>
          Page {currentPage + 1} of {totalPages}
        </span>
        {currentPage < totalPages - 1 && (
          <button onClick={() => setCurrentPage(currentPage + 1)}>
            Next
          </button>
        )}
      </div>

      {/* Test Feedback */}
      {testScore !== null && (
        <div className="test-feedback">
          <p>
            You scored {testScore}% {' '}
            {testScore === 100
              ? 'Wordlist completed! Awesome job!'
              : 'Try again to master all words!'}
          </p>
        </div>
      )}

      
    </div>
  );
}

export default WordlistDetail;
