// BatteryChargingComponent.js

import React, { useState, useEffect } from 'react';
import './BatteryChargingComponent.css';

const BatteryChargingComponent = ({ htmlContent }) => {
  const [batteryLevel, setBatteryLevel] = useState(0);
  const [contentVisible, setContentVisible] = useState(false);

  useEffect(() => {
    let interval = null;
    if (batteryLevel < 100) {
      interval = setTimeout(() => {
        setBatteryLevel((prevLevel) => prevLevel + 20);
      }, 500);
    } else {
      // Show the content once charging is complete
      setContentVisible(true);
    }

    return () => {
      if (interval) clearTimeout(interval);
    };
  }, [batteryLevel]);

  return (
    <div className="warming">
      <h3 className="warming-title">Warm-Up Activity</h3>
      <div className="warming-content">
        <div className="battery-container">
          <div className="battery">
            <div className="battery-body">
              <div
                className="battery-level"
                style={{ height: `${batteryLevel}%` }}
              ></div>
            </div>
            <div className="battery-cap"></div>
          </div>
          <div className="battery-label">
            {batteryLevel < 100 ? 'Recharging...' : 'Charging Complete!'}
          </div>
        </div>
        {contentVisible && (
          <div
            className="parsed-content"
            dangerouslySetInnerHTML={{ __html: htmlContent }}
          />
        )}
      </div>
    </div>
  );
};

export default BatteryChargingComponent;
