import React, { useState } from 'react';
import { Link } from 'react-router-dom';

const ProfileSidebar = ({ handleLogout }) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleSidebar = () => {
    setIsOpen(!isOpen);

    // Prevent body scrolling when sidebar is open
    if (!isOpen) {
      document.body.style.overflow = 'hidden';
    } else {
      document.body.style.overflow = 'auto';
    }
  };

  return (
    <>
      {/* Mobile Floating Button */}
      <div
        className="floating-button"
        onClick={toggleSidebar}
        aria-label={isOpen ? 'Close Menu' : 'Open Menu'}
      >
        <span className="material-symbols-outlined">menu</span>
      </div>

      {/* Sidebar */}
      <aside
        className={`sidebar ${isOpen ? 'mobile-open' : ''}`}
        aria-hidden={!isOpen}
      >
        <div className="sidebar-content">
          <SidebarItem icon="person" text="Info" to="/profile" />
          <SidebarItem icon="chevron_right" text="How2Start" />
          <SidebarItem icon="book_5" text="Tests" to="/exam" />
          <SidebarItem icon="people" text="Friends" />
          <SidebarItem icon="account_balance" text="Balance" />
          <SidebarItem icon="monitoring" text="Stats" />
          <SidebarItem icon="settings" text="Settings" />
          <button
            className="sidebar-item logout-button"
            onClick={handleLogout}
            aria-label="Logout"
          >
            <span className="material-symbols-outlined">logout</span>
            Logout
          </button>
        </div>
      </aside>

      {/* Overlay for mobile */}
      {isOpen && (
        <div
          className="sidebar-overlay"
          onClick={toggleSidebar}
          aria-label="Close Menu Overlay"
        ></div>
      )}
    </>
  );
};

const SidebarItem = ({ icon, text, to }) => (
  <Link
    to={to || '#'}
    className="sidebar-item"
    aria-disabled={!to}
    aria-label={text}
  >
    <span className="material-symbols-outlined">{icon}</span>
    {text}
  </Link>
);

export default ProfileSidebar;
